<script>
  import testData from '@/assets/testData.js'
  import fall_view from '@/components/fall_view.vue'
  export default{
    name: 'Falling',
    props: ['cur_year'],
    data (){
        return{
            appearedData: [],
            listData: [],
            cur_era: 3,
            cur_era_year: [],
            cur_era_name: '헌병 경찰 통치기',
            selectedImage: [],
        }
    },
    created() {
        this.listData=testData;
        
    },
    computed: {
        calYear() {
            if (this.cur_year>=1910 && this.cur_year<=1919){
                return this.cur_era=1;
            }
            else if (this.cur_year>=1920 && this.cur_year<=1931){
                return this.cur_era=2;
            }
            if (this.cur_year>=1932){
                return this.cur_era=3;
            }
        },
        calEra() {
            if (this.calYear==1){
                return this.cur_era_name='헌병 경찰 통치기';
            }
            else if (this.calYear==2){
                return this.cur_era_name='문화 통치기';
            }
            else if (this.calYear==3){
                return this.cur_era_name= '민족 말살 통치기';
            }
        }
    },
    
    methods: {
        calData() {
            this.selectedImage= [];
            if (this.calYear==1){
                this.appearedData = this.listData.filter((data) => {
                    if (data.year>=1910 && data.year<=1919){
                        return true;
                    }
                    return false;
                })
            }
            else if (this.calYear==2){
                this.appearedData = this.listData.filter((data) => {
                    if (data.year>=1920 && data.year<=1931){
                        return true;
                    }
                    return false;
                })
            }
            else if (this.calYear==3){
                this.appearedData = this.listData.filter((data) => {
                    if (data.year>=1932){
                        return true;
                    }
                    return false;
                })
            }
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
            var idx = Math.floor(Math.random() * this.appearedData.length);
            this.selectedImage.push(this.appearedData[idx]);
        }
    },
    watch: {
        cur_year: function (value) {
            this.calData()
        }
    }
    
    
  }
</script>

