const testData = [{'id': 1, 'title': '시악시 마음은', 'author': '홍사용', 'year': 1922.0, 'tags': ['반복', '한글', '운문'], 'contents': '시악시 마음은\n비탈길 밭둑에\n삽살이 조을고\n바람이 얄궂어\n시악시 마음은\n………………\n\n찢어 내려라\n버들가지를\n꺾지는 말어요\n비틀어 다고\n\n시들픈 나물은\n뜯거나 말거나\n늬나나 나……\n나나나 늬……'}, {'id': 2, 'title': '봄은 가더이다', 'author': '홍사용', 'year': 1922.0, 'tags': ['한글', '산문'], 'contents': '봄은 가더이다\n\n"거져 믿어라 "\n봄이나 꽃이나 눈물이나 슬픔이나\n온갖 세상(世上)을, 거저나 믿을까?\n에라 믿어라, 더구나 믿을 수 없다는\n젊은이들의 풋사랑을\n\n봄은 오더니만, 그리고 또 가더이다\n꽃은 피더니만, 그리고 또 지더이다\n\n님아 님아 울지 말어라\n봄은 가고 꽃도 지는데\n여기에 시들은 이내 몸을\n왜 꼬드겨 울리려 하는냐\n\n님은 웃더니만, 그리고 또 울더이다\n\n울기는 울어도 남따라 운다는\n그 설움인 줄은, 알지 말아라\n그래도 또, 웃지도 못하는 내 간장(肝臟) 이로다\n그러나 어리다, 연정아(軟情兒) 의 속이여\n꽃이 날 위해 피었으랴? 그렇지 않으면\n꽃이 날 위해 진다더냐? 그렇지 않으면\n핀다고 좋아서 날뛸 인 누구며\n진다고 서러워 못 살 인 누군고\n\n"시절이 좋다" 떠들어대는\n봄 나들이 소리도, 을씨년스럽다\n산(山)에 가자 물에 가자\n그리고 또 어데로\n"봄에 놀아난 호드기 소리를\n마디마디 꺾지를 마소\n잡아뜯어라, 시원치 않은 꽃가지"\n들 바구니 나물꾼 소리도\n눈물은 그것도 눈물이더라\n\n바람이 소리없이 지나갈 때는\n우리도 자취없이 만날 때였다\n청(請)치도 않는, 너털웃음을\n누구는 일부러 웃더라마는\n내가 어리석어 말도 못할 제\n휠휠 벗어버리는, 분홍(粉紅) 치마는\n"봄바람이 몹시 분다" 핑계이더라\n\n이게 사랑인가 꿈인가\n꿈이 아니면 사랑이리라\n사랑도 꿈도 아니면, 아지랭이인가요\n허물어진 돌무더기에, 아지랭이인 게지요\n그것도 아니라, 내가 속앗음이로다\n\n동무야, 비웃지 마라\n아차, 꺾어서 시들었다고\n내가 차마, 꺾기야 하였으랴만\n어여쁜 그 꽃을, 아끼어 준들\n흉보지 마라, 꽃이나 나늘\n안타까운 가슴에, 부여안았지\n\n그러나 그는, 꺾지 않아도\n저절로 스러지는 제 버릇이라네\n아그런들 그 꽃이 차마\n차마, 졌기야 하였으랴만\n무디인 내 눈에 눈물이 어리어\n아마도, 아니 보이던 게로다\n아그러나, 봄은 오더니만, 그리고 또 가더이다'}, {'id': 3, 'title': '백조는 흐르는데 별 하나 나 하나', 'author': '홍사용', 'year': 1922.0, 'tags': ['한글', '산문'], 'contents': '저\xad기 저 하늘에서 춤추는 저것은 무어?\n오\xad금빛 노을!\n나의 가슴은 군성거리며 견딜 수 없습니다\n앞 강에서 일상(日常) 부르는 우렁찬 소리가\n어여쁜 나를 불러냅니다.\n귀에 익은 음성이 머얼리서 들릴 때에\n철없는 마음은 좋아라고 미쳐서 잔디밭 모래톱으로 줄달음칩니다.\n\n이러다 다리 뻗고 주저앉아서 일없이 지껄입니다.\n은(銀) 고리같이 동글고 매끄러운 혼자 이야기를,\n상글상글하는 태백성(太白星)이 머리 위에 반짝이니,\n벌써 반가운 이가 반가운 그이가 옴이로소이다\n분(粉) 세수한 듯한 오리알빛 동그레 달이\n앞 동산 봉우릴 짚고서 방그레\xad 바시시 솟아오리며,\n바시락거리는 깁 안개 위으로 달콤한 저녁의 막(幕)이\n소리를 쳐 내려올 때에 너른너른하는\n허\xad연 밀물이 팔 벌려 어렴풋이 닥쳐옵니다.\n\n이때 올시다. 이때면은 나의 가슴은 더욱더욱 뜁니다.\n어두운 수풀 저쪽에서 어른거리는\n검은 그림자를 무서워 그럼이 아니라 자글대는 내 얼굴을\n물끄러미 보다가 넌지시 낯 숙여 웃으시는 그이를\n풋여린 마음이 수줍어 언뜻 봄이로소이다.\n\n신부(新婦)의 고요히 휩싸는 치맛자락같이\n달 잠겨 떨리는 잔살 물결이 소리없이 어린이의 신흥(新興)을\n흐느적거리니 물고기같이 내닫는 가슴을 걷잡을 수 없어\n물빛도 은(銀) 같고물소리도 은(銀)같은 가없는\n희열(喜悅) 나라로 더벅더벅 걸어갑니다 .\n미칠듯이 자지러져 철철 흐르는 기쁨에 뛰여서\xad.\n\n아\xad 끝없는 기쁨이로소이다.\n나는 하고 싶은 소리를 다 불러봅니다.\n이러다 정처(定處) 없는 감락(甘樂)이 온몸을 고달프게 합니다.\n그러면 안으려고 기다리는 이에게\n팔 벌려 안기듯이 어릿광처럼 힘없이 넘어 집니다.\n\n옳지 이러면 공단(貢緞) 같이 고운 물결이\n찰락찰락 나의 몸을 쓰담아 주노나!\n커다란 침묵은 길이길이 조으는데 끝없이 흐르는\n밀물 나라에는 낯익은 별하나가 새로이 비췹니다.\n거기서 웃음 섞어 부르는 자장노래는\n다소이 어리인 금빛 꿈터에 호랑나비처럼 훨훨 날아 듭니다.\n\n어쩌노! 이를 어쩌노 아\xad어쩌노!\n어머니 젖을 만지 듯한 달콤한 비애(悲哀)가\n안개처럼 이 어린 넋을 휩싸들으니.\n심술스러운 응석을 숨길 수 없어 아니한 울음을 소리쳐 웁니다.'}, {'id': 4, 'title': '강 배', 'author': '한용운', 'year': 1936.0, 'tags': ['한글', '운문'], 'contents': '저녁 빛을 배불리 받고\n거슬러 오는 적은 배는\n온 江[강]의 맑은 바람을\n한 돛에 가득히 실었다.\n구슬픈 노 젓는 소리는\n봄하늘에 사라지는데\n江[강]가의 술집에서\n어떤 사람이 손짓을 한다.'}, {'id': 5, 'title': '낙화', 'author': '한용운', 'year': 1936.0, 'tags': ['반복', '한글', '운문'], 'contents': '떨어진 꽃이 힘없이 大地[대지]의 품에 안길 때\n애처로운 남은 香氣[향기]가 어대로 가는 줄을 나는 안다.\n가는 바람이 적은 풀과 속삭이는 곳으로 가는 줄을 안다.\n떨어진 꽃이 굴러서 알지도 못하는 집의 울타리 새이로 들어갈 때에,\n쇠잔한 붉은 빛이 어대로 가는 줄을 나는 안다.\n떨어진 꽃이 날려서 적은 언덕을 넘어갈 때에,\n가엾은 그림자가 어대로 가는 줄을 나는 안다.\n봄을 빼앗아 가는 惡魔[악마]의 발밑으로 사라지는 줄을 안다.'}, {'id': 6, 'title': '사랑하는 까닭', 'author': '한용운', 'year': 1926.0, 'tags': ['반복', '한글', '산문'], 'contents': '내가 당신을 사랑하는 것은\n까닭이 없는 것은 아닙니다.\n다른 사람들은 나의 홍안만을 사랑하지만은\n당신은 나의 백발도 사랑하는 까닭입니다.\n\n내가 당신을 사랑하는 것은\n까닭이 없는 것은 아닙니다.\n다른 사람들은 나의 미소만을 사랑하지만은\n당신은 나의 눈물도 사랑하는 까닭입니다.\n\n내가 당신을 사랑하는 것은\n까닭이 없는 것은 아닙니다.\n다른 사람들은 나의 건강만을 사랑하지만은\n당신은 나의 죽음도 사랑하는 까닭입니다'}, {'id': 7, 'title': '인과율', 'author': '한용운', 'year': 1926.0, 'tags': ['한글', '산문'], 'contents': '당신은 옛 맹세를 깨치고 가십니다.\n당신의 맹세는 얼마나 참되었습니까.\n그 맹세를 깨치고 가는 이별은 믿을 수가 없습니다.\n참 맹세를 깨치고 가는 이별은 옛 맹세로 돌아올 줄을 압니다.\n그것은 엄숙한 인과율입니다.\n나는 당신과 떠날 때에 입맞춘 입술이 마르기 전에\n당신이 돌아와서 다시 입맞추기를 기다립니다.\n\n그러나 당신이 가시는 것은 옛 맹세를 깨치려는\n고의가 아닌 줄을 나는 압니다.\n\n비록 당신이 지금의 이별을 영원히 깨치지 않는다 하여도,\n당신의 최후의 접촉을 받은 나의 입술을\n다른 남자의 입술에 댈 수는 없습니다.'}, {'id': 8, 'title': '조춘', 'author': '한용운', 'year': 1936.0, 'tags': ['한글', '산문'], 'contents': '이른 봄 적은 언덕\n쌓인 눈을 저어 마소\n제 아무리 차다기로\n돋은 엄을 어이 하리\n봄옷을 새로 지어\n가신 님께 보내고저\n\n새 봄이 오단말가\n매화야 물어보자.\n눈바람에 막힌 길을\n제 어이 오단말가.\n매화는 말이 없고\n봉오리만 맺더라\n\n봄동산 눈이 녹아\n꽃뿌리를 적시도다.\n찬바람에 못견대는\n어엽분 꽃나무야\n간 겨울 나리는 눈이\n봄의 使徒[사도]이니라.'}, {'id': 9, 'title': '가을 뜻', 'author': '최남선', 'year': 1908.0, 'tags': ['한글', '산문'], 'contents': '衰[쇠]한버들 말은풀 맑은시내에\n배가불은 흔돗다라 가난뎌배야\n世上是非[세상시비] 더뎌두고 어늬곳으로\n너혼댜만 무엇실코 도망하나냐\n\n나의 배에 실은것은 다른것업서\n四面[사면]에서 엇어온바 새消息[소식]이니\n杜門洞[두문동]속 캄캄한데 코를부시난\n山林學者[산임학자] 兩班[양반]들끠 傳[전]하려하오。'}, {'id': 10, 'title': '고향', 'author': '정지용', 'year': 1932.0, 'tags': ['한글', '운문'], 'contents': '고향에 고향에 돌아와도\n그리던 고향은 아니러뇨.\n\n산꿩이 알을 품고\n뻐꾸기 제철에 울건만.\n\n마음은 제 고향 지니지 않고\n머언 항구로 떠도는 구름.\n\n오늘도 뫼끝에 홀로 오르니\n흰 점꽃이 인정스레 웃고,\n\n어린 시절에 불던 풀피리 소리 아니 나고\n메마른 입술에 쓰디쓰다.\n\n고향에 고향에 돌아와도\n그리던 하늘만이 높푸르구나.'}, {'id': 11, 'title': '버레우는 소리', 'author': '이장희', 'year': 1929.0, 'tags': ['한글', '운문'], 'contents': '밤마다 울든 저버레는\n오늘도 마루미테서 울고잇네\n\n저녁에 빗나는 냇물가치\n버레 우는 소리는 차고도 쓸쓸하여라\n\n밤마다 마루미테서 우는 버레소리에\n내마음 한업시 이끌리나니'}, {'id': 12, 'title': '겨울의 모경', 'author': '이장희', 'year': 1927.0, 'tags': ['한글', '한자', '산문'], 'contents': '큰 거리는 저물은 연귀에 저저 動靜이 몽롱하고\n녹설은 무쇠가튼 鈍重한 냄새가 잠겨 흐른다\n그러나 가다가는 알는 소리 은은한 電車가\n물오른 풀입가튼 뾰죽한 神經을 들어내고\n쌔안인 푸른꼿을 虛空에 날니기도 한다\n길바닥은 얼어서 죽은 구렁이가티 뻐드러젓고\n그우를 새찬 바람이 돗을 달고 다르나면\n야릇한 군소리가 눈물에 떨어 그윽히 들닌다\n잘 지절대고 하이카라인 재비의 幽靈이\n불눅한 검증 外套를 휘감고 비털거리는 사이에 잇서서\n흐린 銀ㅅ결가티 희수름한 옷 그림자가 고요히 움즉인다\n구름인지 안개인지 넘으로 핏줄 선 눈알가티 붉으레함은\n마즈막으로 넘어가는 날볏의 얼굴이 숨어 잇슴이라\n이들 눈에 드는 모든 것이 저마다 김을 뿜어서\n그는 幻燈의 映寫膜이며 沈鬱한 뗏산을 보는듯하다'}, {'id': 13, 'title': '겨울밤', 'author': '이장희', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '눈 비는 개였으나\n흰 바람은 보이듯하고\n싸늘한 등불은 거리에 흘러\n거리는 푸르른 琉璃창\n검은 銳角이 미끄러 간다.\n\n고드름 매달린\n저기 저 처마 밑에\n서울의 亡靈이 떨고 있다.\n풍지같이 떨고 있다.'}, {'id': 14, 'title': '고양이의 꿈', 'author': '이장희', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '시내 우에 돌다리\n달아래 버드나무\n봄안개 어리인 시냇가에, 푸른 고양이\n곱다랗게 단장하고 빗겨 있소. 울고 있소.\n기름진 꼬리를 치들고\n\n밝은 애달픈 노래를 부르지요.\n푸른 고양이는 물올은 버드나무에 스르를 올나가\n버들가지를 안고 버들가지를 흔들며\n또 목노아 웁니다, 노래를 불음니다.\n\n멀리서 검은 그림자가 움직이고\n칼날이 銀 같이 번쩍이더니\n푸른 고양이도 볼 수 없고,\n꽃다운 소리도 들을 수 없고\n그저 쓸쓸한 모래 위에 鮮血이 흘러있소.'}, {'id': 15, 'title': '봄은 고양이로다', 'author': '이장희', 'year': 1924.0, 'tags': ['한글', '반복', '운문'], 'contents': '꽃가루와 같이 부드러운 고양이의 털에\n고운 봄의 향기가 어리우도다\n\n금방울과 같이 호동그란 고양이의 눈에\n미친 봄의 불길이 흐르도다\n\n고요히 다물은 고양이의 입술에\n포근한 봄의 졸음이 떠돌아라\n\n날카롭게 쭉 뻗은 고양이의 수염에\n푸른 봄의 생기가 뛰놀아라'}, {'id': 16, 'title': '나의뮤-즈', 'author': '이육사', 'year': 1946.0, 'tags': ['한글', '산문'], 'contents': '아주 헐벗은 나의 뮤―즈는\n한번도 기야 싶은 날이 없어\n사뭇 밤만을 왕자(王者)처럼 누려왔소\n\n아무것도 없는 주제언만도\n모든 것이 제 것인듯 버티는 멋이야\n그냥 인드라의 영토(領土)를 날아도 다닌다오\n\n고향은 어데라 물어도 말은 않지만\n처음은 정녕 북해안(北海岸) 매운 바람속에 자라\n대곤(大鯤)을 타고 다녔단 것이 일생(一生)의 자랑이죠\n\n계집을 사랑커든 수염이 너무 주체스럽다도\n취(醉)하면 행랑 뒷골목을 돌아서 다니며\n복보다 크고 흰 귀를 자조 망토로 가리오\n\n그러나 나와는 몇 천겁(千劫) 동안이나\n바루 비취(翡翠)가 녹아나는 듯한 돌샘가에\n향연(饗宴)이 벌어지면 부르는 노래란 목청이 외곬수요\n\n밤도 지진하고 닭소래 들릴 때면\n그만 그는 별 계단(階段)을 성큼성큼 올라가고\n나는 촛불도 꺼져 백합(百合)꽃밭에 옷깃이 젖도록 잤소'}, {'id': 17, 'title': '광야', 'author': '이육사', 'year': 1945.0, 'tags': ['한글', '운문'], 'contents': '까마득한 날에\n하늘이 처음 열리고\n어데 닭 우는 소리 들렸으랴\n\n모든 산맥들이\n바다를 연모해 휘달릴 때도\n차마 이곳을 범(犯)하던 못하였으리라\n\n끊임없는 광음(光陰)을\n부지런한 계절이 피어선 지고\n큰 강물이 비로소 길을 열었다\n\n지금 눈 내리고\n매화 향기 홀로 아득하니\n내 여기 가난한 노래의 씨를 뿌려라.\n\n다시 천고(千古)의 뒤에\n백마 타고 오는 초인(超人)이 있어\n이 광야에서 목놓아 부르게 하리라'}, {'id': 18, 'title': '꽃', 'author': '이육사', 'year': 1945.0, 'tags': ['한글', '반복', '운문'], 'contents': '동방은 하늘도 다 끝나고\n비 한방울 나리쟎는 그 땅에도\n오히려 꽃은 빨갛게 피지 않는가\n내 목숨을 꾸며 쉬임없는 날이여\n\n북(北)쪽「툰드라」에도 찬 새벽은\n눈 속 깊이 꽃 맹아리가 옴작거려\n제비떼 까맣게 날라오길 기다리나니\n마침내 저버리지 못할 약속(約束)이여!\n\n한바다 복판 용솟음치는 곳\n바람결 따라 타오르는 꽃성(城)에는\n나비처럼 취(醉)하는 회상(回想)의 무리들아\n오늘 내 여기서 너를 불러 보노라'}, {'id': 19, 'title': '주난흥여', 'author': '이육사', 'year': 1943.0, 'tags': ['한글', '운문'], 'contents': '술기운과 시정(時情)이 두 가지 한창인데\n북두성은 돌고 달은 난간에 가득하다\n하늘 끝 만리 뜻을 아는 이 있으니\n늙은 돌 맑은 안개가 나로 하여금 차게 하더라'}, {'id': 20, 'title': '서울', 'author': '이육사', 'year': 1941.0, 'tags': ['한글', '반복', '운문'], 'contents': '어떤 시골이라도 어린애들은 있어 고놈들 꿈결조차\n잊지 못할 자랑속에 피어나 황홀하기 장미(薔薇)빛 바다였다.\n\n밤마다 야광(夜光)충들의 고운 불 아래 모여서 영화로운 잔체와\n쉴새없는 해조(諧調)에 따라 푸른 하늘을 꾀했다는 이야기.\n\n온 누리의 심장을 거기에 느껴 보겠다고 모든 길과 길들\n핏줄같이 엉클여서 역(驛)마다 느릅나무가 늘어서고\n\n긴 세월이 맴도는 그 판에 고추 먹고 뱅―뱅 찔레 먹고\n뱅―뱅 넘어지면 「맘모스」의 해골(骸骨)처럼 흐르는 인광(憐光) 길다랗게.\n\n개아미 마치 개아미다 젊은놈들 겁이 잔뜩 나 차마 차마\n하는 마음은 널 원망에 비겨 잊을 것이었다 깍쟁이.\n\n언제나 여름이 오면 황혼의 이 뿔따귀 저 뿔따귀에\n한 줄씩 걸쳐매고 짐짓 창공에 노려대는 거미집이다 텅 비인.\n\n제발 바람이 세차게 불거든 케케묵은 먼지를 눈보라마냥\n날려라 녹아나리면 개천에 고놈 살모사들 승천을 할는지.'}, {'id': 21, 'title': '아미', 'author': '이육사', 'year': 1941.0, 'tags': ['한글', '반복', '운문'], 'contents': '향수(鄕愁)에 철나면 눈섶이 기나니요\n바다랑 바람이랑 그 사이 태어났고\n나라마다 어진 풍속에 자랐겠죠.\n\n짓푸른 깁장(帳)을 나서면 그 몸매\n하이얀 깃옷은 휘둘러 눈부시고\n정녕 「왈쓰」라도 추실란가봐요.\n\n햇살같이 펼쳐진 부채는 감춰도\n도톰한 손결야 교소(驕笑)를 가루어서\n공주의 홀(笏)보다 깨끗이 떨리오.\n\n언제나 모듬에 지쳐서 돌아오면\n꽃다발 향기조차 기억만 서러워라\n찬 젓대소리에다 옷끈을 흘려보내고.\n\n촛불처럼 타오른 가슴속 사념(思念)은\n진정 누구를 애끼시는 속죄(贖罪)라오\n발 아래 가득히 황혼이 나우리치오\n\n달빛은 서늘한 원주(圓柱)아래 듭시면\n장미(薔薇)쩌 이고 장미쪄 흩으시고\n아련히 가시는 곳 그 어딘가 보이오.'}, {'id': 22, 'title': '빼앗긴 들에도 봄은 오는가', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '반복', '산문'], 'contents': 'ᄲᅢ앗긴들에도, 봄은오는가\n\n\n지금은 남의ᄯᅡᆼ―ᄲᅢ앗긴들에도 봄은오는가?\n\n나는 온몸에 해살을 밧고\n푸른한울 푸른들이 맛부튼 곳으로\n가름아가튼 논길을ᄯᅡ라 ᄭᅮᆷ속을가듯 거러만간다.\n\n입슐을 다문 한울아 들아\n내맘에는 내혼자온 것 갓지를 안쿠나\n네가ᄭᅳᆯ엇느냐 누가부르드냐 답답워라 말을해다오.\n\n바람은 내귀에 속삭이며\n한자욱도 섯지마라 옷자락을 흔들고\n종조리는 울타리넘의 아씨가티 구름뒤에서 반갑다웃네.\n\n고맙게 잘자란 보리밧아\n간밤 자정이넘어 나리는 곱은비로\n너는 삼단가튼머리를 ᄭᅡᆷ앗구나 내머리조차 갑븐하다.\n\n혼자라도 갓부게나 가자\n마른논을 안고도는 착한도랑이\n젓먹이 달래는 노래를하고 제혼자 엇게춤만 추고가네.\n\n나비 제비야 ᄭᅡᆸ치지마라\n맨드램이 들마ᄭᅩᆺ애도 인사를해야지\n아주ᄭᅡ리 기름을바른이가 지심매는 그들이라 다보고십다.\n\n내손에 호미를 쥐여다오\n살ᄶᅵᆫ 젓가슴과가튼 부드러운 이흙을\n발목이 시도록 밟어도보고 조흔ᄯᅡᆷ조차 흘리고십다.\n\n강가에 나온 아해와가티\nᄶᅡᆷ도모르고 ᄭᅳᆺ도업시 닷는 내혼아\n무엇을찻느냐 어데로간냐 웃어웁다 답을하려무나.\n\n나는 온몸에 풋내를 ᄯᅵ고\n푸른웃음 푸른설음 어우러진사이로\n다리를절며 하로를것는다 아마도 봄신령이집혓나보다.\n그러나 지금은―들을 ᄲᅢ앗겨 봄조차 ᄲᅢ앗기것네'}, {'id': 23, 'title': '곡자사', 'author': '이상화', 'year': 1929.0, 'tags': ['한글', '산문'], 'contents': '웅희야! 너는 갔구나.\n엄마가 뉜지 아빠가 뉜지\n너는 모르고 어디로 갔구나.\n\n불쌍한 어미를 가졌기 때문에\n가난한 아비를 두었기 때문에\n오자마자 네가 갔구나.\n\n달보다 잘 났던 우리 웅희야\n부처님보다도 착하던 웅희야\n너를 언제나 안아나 줄꼬.\n\n그러께 팔월에 네가 간 뒤\n그 해 시월에 내가 갇히어\n네 어미 간장을 태웠더니라.\n\n지나간 오월에 너를 업고서\n네 어미가 정신도 못차린 첫 칠날\n네 아비는 또다시 갇히었더니라.\n\n그런 뒤 오온 한 해도 못 되어\n갖은 꿈 온갖 힘 다 쓰려던\n이 아비를 바리고 너는 갔구나.\n\n불쌍한 속에서 네가 태어나\n불쌍한 한숨에 휩쌔고 말 것\n어미 아비 두 가슴에 못이 박힌다.\n\n말 못하던 너일망장 잘 웃기 따에\n장차는 어려움없이 잘 지내다가\n사내답게 한평생을 마칠 줄 알았지.\n\n귀여운 네 발에 흙도 못 묻혀\n몹쓸 이런 변이 우리에게 온 것\n아, 마른 하늘 벼락에다 어이 견주라.\n\n너를 위해 얽던 꿈 어디 쓰고\n네게만 쏟던 사랑 어디 줄꼬.\n웅희야, 제발 다시 숨쉬어 다오.\n\n하루 해를 네 곁에서 못 지내 본 것\n한 가지도 속시원히 못해 준 것\n감옥방 판자벽이 얼마나 울었던지\n\n웅희야! 너는 갔구나\n웃지도 울지도 꼼짝도 않고.\n\n불쌍한 선물로 설움을 끼고[1]\n가난한 선물로 몹쓸 병 안고\n오자마자 네가 갔구나.\n\n하늘보다 더 미덥던 우리 웅희야\n이 세상엔 하나밖에 없던 웅희야\n너를 언제나 안아나 줄고 ――'}, {'id': 24, 'title': '설어운 조화', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '산문'], 'contents': '일은봄 말업는 한울은\n한숨을 지여보아도 나즌텬정과가티 가위만눌린다.\n낫고도놉흔 그한울로\n솔개한마리가 제비가되여\n｢서울장안｣우에서 점은쇠북을탁고 ᄯᅥ도라다니니\n비웃는듯 세상을 조상하는가보다\n일은봄 힘업는 이ᄯᅡᆼ은\n발버둥을 쳐보아도 죽은무덤과가티 가위만눌린다.'}, {'id': 25, 'title': '이 해를 보내는 노래', 'author': '이상화', 'year': 1925.0, 'tags': ['한글', '산문'], 'contents': '「가뭄이 들고 큰물이 지고 불이 나고 목숨이 많이 죽은 올해이다. 조선 사람아, 금강산에 불이 났단 이 한 말이 얼마나 깊은 묵시인가. 몸서리치이는 말이 아니냐. 오, 하나님――사람의 약한 마음이 만든 도꺠비가 아니라 우리에게 힘을 주는 자연의 영정 하나뿐인 사람의 예지――를 불러 말하노니, 잘못 짐작은 갖지 말고 보아라 이 해가 다 가기 전에 조선 사람의 가슴마다에 숨어 사는 모든 하나님들아!」\n\n하나님! 나는 당신께 돌려 보냅니다.\n속썩은 한숨과 피젖은 눈물로 이 해를 싸서\n웃고 받을지 울고 받을지 모르는 당신께 돌려 보냅니다.\n당신이 보낸 이 해는 목마르던 나를 물에 빠져 죽이려다가\n누더기로 겨우 가린 헐벗은 몸을 태우려도 하였고\n주리고 주려서 사람끼리 원망타가 굶어 죽고 만 이 해를 돌려 보냅니다.\n하나님! 나는 당신께 묻잡으려 합니다.\n땅에 엎드려 하늘을 우러러 창잡은 손으로\n밉게 들을지 섧게 들을지 모르는 당신께 묻잡으려 합니다.\n당신 보낸 이 해는 우리에게 「노아의 홍수」를 갖고 왔다가\n그날의 「유황불」은 사람도 만들 수 있다 태워 보였으나\n주리고 주려도 우리들이 못 깨쳤다 굶어 죽였던가 묻잡으려 합니다.\n아, 하나님!\n이 해를 받으시고 오는 새해 아침부턴 벼락을 내려줍소.\n악도 선보담 더 착할 때 있음을 아옵든지 모르면 죽으리라.'}, {'id': 26, 'title': '서러운 해조', 'author': '이상화', 'year': 1940.0, 'tags': ['한글', '반복', '운문'], 'contents': '하이얗던 해는\n떨어지려 하여\n헐떡이며\n피 뭉텅이가 되다.\n\n새빨갛던 마음은\n늙어지려 하여\n곯아지며\n굼벵이 집이 되다.\n\n하루 가운데\n오는 저녁은\n너그럽다는 하늘의\n못 속일 멍통일러라.\n\n일생 가운데\n오는 젊음은\n복스럽다는 인간의\n못 감출 설움일러라.\n라이선스[편집]'}, {'id': 27, 'title': '나는 해를 먹다', 'author': '이상화', 'year': 1935.0, 'tags': ['한글', '산문'], 'contents': '구름은 차림옷에 놓기 알맞아 보이고\n하늘은 받다같이 깊다란하다.\n\n한낮 뙤약볕이 쬐는지도 모르고\n온 몸이, 아니 넋조차 깨온 아찔하여지도록\n뼈져리는 좋은 맛에 자지러지기는\n보기 좋게 잘도 자란 과수원의 목거지다.\n\n배추 속처럼 핏기 없는 얼굴에도\n푸른 빛이 비치어 생기를 띠고\n더구나 가슴에는 깨끗한 가을 입김을 안은 채\n능금을 부수노라 해를 지우나니.\n\n나뭇가지를 더우잡고 발을 뻗기도 하면서\n무성한 나뭇잎 속에 숨어 수줍어하는\n탐스럽게도 잘도 익은 과일을 찾아\n위태로운 이 짓에 가슴을 조이는 이 떄의 마음 저 하늘같이 맑기도 하다.\n\n머리가닥 같은 실바람이 아무리 나부껴도\n메밀꽃밭에 춤추던 벌들이 아무리 울어도\n지는 날 예쁜이들 그리어 살며시 눈물지는,\n그런 생각은 꿈 밖에 꿈으로도 보이지 않는다.\n\n남의 과일밭에 몰래 들어가\n험상스런 얼굴과 억센 주먹을 두려워하면서\n하나 둘 몰래 훔치던 어릴 적 철없던 마음이 다시 살아나자\n그립고 우습고 죄 없던 그 기쁨이 오늘에도 있다.\n\n부드럽게 쌓여 있는 이랑의 흙은\n솥뚜껑을 열고 밥김을 맡는 듯 구수도 하고\n나무에 달린 과일――푸른 그릇에 담긴 깍두기같이\n입안에 맑은 침을 자아내나니.\n\n첫 가을! 금호강[1] 굽이쳐 흐르고\n이 벌판 한가운데 주저앉아서\n두 볼이 비자웁게 해 같은 능금을 나는 먹는다.'}, {'id': 28, 'title': '겨울 마음', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '운문'], 'contents': '물장수가 귓속으로 들어와 내 눈을 열었다.\n보아라!\n까치가 뼈만 남은 나뭇가지에서 울음을 운다.\n왜 이래?\n서리가 덩달아 추녀끝으로 눈물을 흘리는가.\n내야 반가웁기만 하다. 오늘은 따스하겠구나.'}, {'id': 29, 'title': '시인에게', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '반복', '산문'], 'contents': '한 편의 시 그것으로\n새로운 세계 하나를 낳아야 할 줄 깨칠 그 때라야\n시인아, 너의 존재가\n비로소 우주에게 없지 못할 너로 알려질것이다,\n가뭄 든 논에게는 청개구리의 울음이 있어야 하듯.\n\n새 세계란 속에서도\n마음과 몸이 갈려 사는 줄 풍류만 나와 보아라.\n시인아, 너의 목숨은\n진저리나는 절룸발이 노릇을 아직도 하는 것이다.\n언제든지 일식된 해가 돋으면 뭣하며 진들 어떠랴\n시인아, 너의 영광은\n미친 개 꼬리도 밟는 어린해의 짬 없는 그 마음이 되어\n밤이라도 낮이라도\n새 세계를 낳으려 손댄 자국이 시가 될 때에 있다.\n촛불로 날아들어 죽어도 아름다운 나비를 보아라.'}, {'id': 30, 'title': '통곡', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '운문'], 'contents': '하늘을 우러러 울기는 하여도\n하늘이 그리워 울음이 아니라\n두 발을 못 뻗는 이 땅이 애달파\n하늘을 흘기니\n울음이 터진다.\n해야 웃지 마라,\n달도 뜨지 마라.'}, {'id': 31, 'title': '병적 계절', 'author': '이상화', 'year': 1925.0, 'tags': ['한글', '반복', '운문'], 'contents': '기러기 제비가 서로 엇갈림이 보기에 이리도 설운가,\n귀뚜리 떨어진 나뭇잎을 부여잡고 긴 밤을 새네.\n가을은 애달픈 목숨이 나뉘어질까 울 시절인가 보다.\n가없는 생각 쌈 모를 꿈이 그만 하나 둘 잦아지려는가,\n흘아비같이 헤매는 바람떼가 한 배 가득 굽이치네.\n가을은 구슬픈 마음이 앓다 못해 날뛸 시절인가 보다.\n하늘을 보아라, 야윈 구름이 떠돌아 다니네.\n땅 위를 보아라, 젊은 조선이 떠돌아 다니네.'}, {'id': 32, 'title': '나의 침실로', 'author': '이상화', 'year': 1923.0, 'tags': ['한글', '반복', '산문'], 'contents': '「마돈나」 지금은 밤도, 모든 목거지에, 다니로라 疲困하여 돌아겨려는도다,\n아, 너도, 먼동이 트기 전으로, 水蜜桃의 네 가슴에, 이슬이 맺도록 달려 오너라.\n\n「마돈나」 오려무나, 네 집에서 눈으로 遺傳하던 眞珠는, 다 두고 몸만 오너라,\n빨리 가자, 우리는 밝음이 오면, 어딘지도 모르게 숨는 두 별이어라.\n\n「마돈나」 구석지고도 어둔 마음의 거리에서, 나는 두려워 떨며 기다리노라,\n아, 어느덧 첫닭이 울고-뭇 개가 짖도다, 나의 아씨여, 너도 듣느냐.\n\n「마돈나」 지난 밤이 새도록, 내 손수 닦아둔 寢室로 가자, 寢室로 !\n낡은 달은 빠지려는데, 내 귀가 듣는 발자욱-오, 너의 것이냐?\n\n「마돈나」 짧은 심지를 더우잡고, 눈물도 없이 하소연하는 내 맘의 燭불을 봐라,\n羊털 같은 바람결에도 窒息이 되어, 얄푸른 연기로 꺼지려는도다.\n\n「마돈나」 오너라 가자, 앞산 그르매가, 도깨비처럼, 발도 없이 이곳 가까이 오도다,\n아, 행여나, 누가 볼는지-가슴이 뛰누나, 나의 아씨여, 너를 부른다.\n\n「마돈나」 날이 새련다, 빨리 오려무나, 寺院의 쇠북이, 우리를 비웃기 전에\n네 손이 내 목을 안아라, 우리도 이 밤과 같이, 오랜 나라로 가고 말자.\n\n「마돈나」 뉘우침과 두려움의 외나무다리 건너 있는 내 寢室 열 이도 없느니 !\n아, 바람이 불도다, 그와 같이 가볍게 오려무나, 나의 아씨여, 네가 오느냐?\n\n「마돈나」 가엾어라, 나는 미치고 말았는가, 없는 소리를 내 귀가 들음은,\n내 몸에 피란 피-가슴의 샘이, 말라버린 듯, 마음과 목이 타려는도다.\n\n「마돈나」 언젠들 안 갈 수 있으랴, 갈테면, 우리가 가자, 끄을려 가지 말고 !\n\n너는 내 말을 믿는 「마돈나」-내 寢室이 復活의 洞窟임을 네야 알련만……\n\n「마돈나」 밤이 주는 꿈, 우리가 얽는 꿈, 사람이 안고 궁그는 목숨의 꿈이 다르지 않으니,\n\n아, 어린애 가슴처럼 歲月 모르는 나의 寢室로 가자, 아름답고 오랜 거기로.\n\n「마돈나」 별들의 웃음도 흐려지려 하고, 어둔 밤 물결도 잦아지려는도다,\n아, 안개가 사라지기 전으로, 네가 와야지, 나의 아씨여, 너를 부른다.'}, {'id': 33, 'title': '원시적 읍울', 'author': '이상화', 'year': 1922.0, 'tags': ['한글', '산문'], 'contents': '방랑성(放浪性)을 품은 에머랄드 널판의 바다가 말없이 엎디였음이\n산 머리에서 늦여름의 한낮 숲을 보는 듯- 조으는 얼굴일러라.\n짜증나게도 늘어진 봄날- 오후(午後)의 하늘이야 희기도 하여라.\n거기에선 이따금 어머니의 젖꼭지를 빠는 어린애 숨결이 날려 오도다.\n사선(斜線) 언덕 위로 쭈그리고 앉은 두어 집 울타리마다\n걸어 둔 그물에 틈틈이 끼인 조개 껍질은 머-ㄹ리서 웃는 이빨일러라.\n마을 앞으로 엎디어 있는 모래 길에는 아무도 없구나.\n지난밤 밤낚기에 나른하여- 낮잠의 단술을 마심인가보다.\n다만 두서넛 젊은 아낙네들이 붉은 치마 입은 허리에 광주리를 달고\n바다의 꿈같은 미역을 걷으며 여울 돌에서 여울 돌로 건너만 간다.\n잠결에 듣는 듯한 뻐꾸기의 부드럽고도 구슬픈 울음소리에\n늙은 삽사리 목을 뻗고 살피다간 다시 눈감고 졸더라.\n나의 가슴엔 갈매기 떼와 수평선(水平線)밖으로 넘어가는 마음과\n넋잃은 시선(視線)-어느 것 보이지도 보려도 안는 물 같은 생각의 구름만 쌓일 뿐이어라.'}, {'id': 34, 'title': '가을의 풍경', 'author': '이상화', 'year': 1922.0, 'tags': ['한글', '반복', '산문'], 'contents': '맥풀린 햇살에 번쩍이는 나무는 선명하기 동양화일러라.\n흙은 아낙네를 감은 천아융(天鵝絨) 허리띠같이 따스워라.\n\n\n무거워가는 나비 나래는 드물고도 쇠하여라.\n아, 멀리서 부는 피리 소린가? 하늘 바다에서 헤엄질치다.\n\n\n병들어 힘없이도 섰는 잔디풀 - 나뭇가지로\n미풍의 한숨은 가는(細) 목을 매고 껄떡이어라.\n\n\n참새 소리는 제 소리의 몸짓과 함께 가볍게 놀고\n온실 같은 마루 끝에 누운 검은 괴의 등은 부드럽기도 기름져라.\n\n\n청춘을 잃어버린 낙엽은 미친 듯 나부끼어라.\n서럽고도 즐겁게 조을음 오는 적막이 더부렁거리다.\n\n\n사람은 부질없는 가슴에다 까닭도 모르는 그리움을 안고\n마음과 눈으로 지나간 푸름의 인상을 허공에다 그리어라.'}, {'id': 35, 'title': '말세의 희탄', 'author': '이상화', 'year': 1922.0, 'tags': ['한글', '반복', '운문'], 'contents': '저녁의 피 묻은 동굴(洞窟)속으로\n아- 밑 없는 그 동굴(洞窟) 속으로\n끝도 모르고\n끝도 모르고\n나는 꺼꾸러지련다.\n나는 파묻히련다.\n가을의 병든 미풍(微風)의 품에다\n아- 꿈꾸는 미풍(微風)의 품에다\n낮도 모르고\n밤도 모르고\n나는 술 취한 집을 세우련다.\n나는 속 아픈 웃음을 빚으련다.'}, {'id': 36, 'title': '가장 비통한 기욕', 'author': '이상화', 'year': 1925.0, 'tags': ['한글', '반복', '산문'], 'contents': '아, 가도다 가도다 쫓겨 가도다.\n망각 속에 있는 간도와 요동벌로\n주린 목숨 움켜쥐고 좇아 가도다.\n자갈을 밥으로 해채를 마셔도\n마구나 가졌으면 단잠을 얽을 것을ㅡ\n인간을 만든 검아 하루 일찍\n차라리 주린 목숨 뺏어 가거라.\n \n아, 사노라 사노라 취해 사노라.\n자폭속에 있는 서울과 시골로\n멍든 목숨 행여 갈까, 취해 사노라.\n어둔 밤 말없는 돌을 안고서\n피울음 울어도 신음은 풀릴 것을ㅡ\n인간을 만든 검아, 하루 일찍\n차라리 취한 목숨 죽여 버려라.'}, {'id': 37, 'title': '몽환병', 'author': '이상화', 'year': 1925.0, 'tags': ['한글', '산문'], 'contents': '목적(目的)도 없는 동경(憧憬)에서 명정(酩酊)하던 하루이었다. 어느 날 한낮에 나는 나의 「에덴」이라던 솔숲 속에 그날도 고요히 생각에 까무러지면서 누워 있었다. 잠도 아니오 죽음도 아닌 침울(沈鬱)이 쏟아지며 그 뒤를 이어선 신비(神秘)로운 변화(變化)가 나의 심령(心靈)위로 덮쳐 왔다.\n나의 생각은 넓은 벌판에서 깊은 구렁으로- 다시 아참 광명(光明)이 춤추는 절정(絶頂)으로- 또다시 끝도 없는 검은 바다에서 낯선 피안(彼岸)으로- 구름과 저녁놀이 흐느끼는 그 피안(彼岸)에서 두려움 없는 주저(躊躇)에 나른하여 눈을 감고 주저앉았다. 오래지 않아 내 마음의 길바닥 위로 어떤 검은 안개 같은 요정(妖精)이 소리도 없이 오만(傲慢)한 보조(步調)로 무엇을 찾는 듯이 돌아다녔다. 그는 모두 검은 의상(衣裳)을 입었는가- 하는 억측(憶觸)이 나기도 하였다. 그때 나의 몸은 갑자기 열병(熱病)든 이의 숨결을 지었다. 온몸에 있던 맥박(脈搏)이 한꺼번에 몰려 가슴을 부술 듯이 뛰놀았다.\n그리하자 보고싶어 번개불같이 일어나는 생각으로 두 눈을 비비면서 그를 보려하였으나 아- 그는 누군지- 무엇인지- 형적(形跡)조차 언제 있었더냐 하는 듯이 사라져 버렸다. 애달프게도 사라져 버렸다.\n다만 나의 기억(記憶)에는 얼굴에까지 흑색(黑色) 면사(面紗)를 쓴 것과 그 면사(面紗) 너머에서 햇살 쪼인 석탄(石炭)과 같은 눈알 두 개의 깜작이던 것뿐이었다. 아무리 보고자 하여도 구름 덮인 겨울과 같은 유장(帷帳)이 안계(眼界)로 전개(展開)될 뿐이었다 발자국 소리나 옷자락 소리조차도 남기지 않았다.\n갈피도- 까닭도 못 잡을 그리움이 내 몸 안과 밖 어느 모퉁이에서나 그칠 줄 모르는 눈물과 같이 흘러내렸다- 흘러내렸다. 숨가쁜 그리움이었다- 못 참을 것이었다.\n아! 요정(妖精)은 전설(傳說)과 같이 갑자기 현현(現顯)하였다. 그는 하얀 의상(衣裳)을 입었다. 그는 우상(偶像)과 같이 방그레 웃을 뿐이었다.- 보얀 얼굴에- 새까만 눈으로 연붉은 입술로- 소리도 없이 웃을 뿐이었다. 나는 청맨관의 시양(視樣)으로 바라보았다.- 들여다 보았다.\n오! 그 얼굴이었다.- 그의 얼굴이었다.- 잊혀지지 않는 그의 얼굴이었다.- 내가 항상 만들어 보던 것이었다.\n목이 메이고 청이 잠겨서 가슴속에 끓는 마음이 말이 되어 나오지 못하고 불김 같은 숨결이 켜이질 뿐이었다. 손도 들리지 않고 발도 떨어지지 않고 가슴 위에 쌓인 바윗돌을 떼밀려고 애쓸 뿐이었다.\n그는 검은 머리를 흩뜨리고 한 걸음- 한 걸음- 걸어 왔다. 나는 놀라운 생각으로 자세히 보았다. 그의 발이 나를 향하고 그의 눈이 나를 부르고 한 자국- 한 자국- 내게로 와 섰다. 무엇을 말할 듯한 입술로 내게로- 내게로 오던 것이다.- 나는 눈이야 찢어져라고 크게만 떠보았다. 눈초리도 이빨도 똑똑히 보였다.\n그러나 갑자기 그는 걸음을 멈추고 입을 다물고 나를 보았다.- 들여다보았다. 아 그 눈이 다른 눈으로 나를 보았다. 내 눈을 뚫을 듯한 무서운 눈이었다. 아 그 눈에서- 무서운 그 눈에서 빗발 같은 눈물이 흘렀다. 까닭 모를 눈물이었다. 답답한 설움이었다.\n여름 새벽 잔디풀 잎사귀에 맺혀서 떨어지는 이슬과 같이 그의 깜고도 가는 속눈썹마다에 수은(水銀)같은 눈물이 방울방울이 달려 있었다. 아깝고 애처로운 그 눈물은 그의 두 볼- 그의 손등에서 반짝이며 다시 고운 때묻은 모시 치마를 적시었다. 아! 입을 벌리고 받아먹고 싶은 귀여운 눈물이었다. 뼈 속에 감추어 두고 싶은 보배로운 눈물이었다.\n그는 어깨를 한두 번 비슥하다가 나를 등지고 돌아섰다. 흩은 머리 숱이 온통 덮은 듯하였다. 나는 능수버들같은 그 머리카락을 안으려 하였다.- 하다못해 어루만져라도 보고 싶었다. 그러나 그는 한 걸음- 두 걸음 저리로 갔다. 어쩔 줄 모르는 설움만을 나의 가슴에 남겨다 두고 한 번이나마 돌아볼 바도 없이 찬찬히 가고만 있었다. 잡을래야 잡을 수 없이 가다간 갑자기 사라져 버렸다. 눈알이 빠진 듯한 어둠뿐이었다. 행여나 하는 맘으로 두 발을 괴고 기다렸었다. 하나 그것은 헛일이었다. 아무것도 보이지 않았다. 이리하여 그는 가고 오지 않았다. 나의 생각엔 곤비(困憊)한 밤의 단꿈 뒤와 같은 추고(追考)- 가상(假想)의 영감(靈感)이 떠돌 뿐이었다. 보다 더 야릇한 것은 그 요정(妖精)이 나오던 그때부터는- 사라진 뒤 오래도록 마음이 미온수(微溫水)에 잠긴 얼음 조각처럼 부류(浮流)가 되며 해이(解弛)되나 그래도 무정방(無定方)으로 욕념(慾念)에도 없는 무엇을 찾는 듯하였다.\n그때 눈과 마음의 렌즈에 영화(映畵)된 것은 다만 장님의 머리 속을 들여다보는 듯한 혼무(混霧)뿐이요 영혼(靈魂)과 입술에는 훈향(薰香)에 미친 나비의 넋 빠진 침묵(沈黙)이 흐를 따름이었다. 그밖엔 오직 망각(忘却)이 이제야 뗀 입 속에서 자체(自體)의 존재(存在)를 인식(認識)하게 된 기억(記憶)으로 거닐 뿐이었다. 나는 저물어 가는 하늘에 조으는 별을 보고 눈물 젖은 소리로 「날은 저물고 밤이 오도다 흐릿한 꿈만 안고 나는 살도다」고 하였다. 아! 한낮에 눈을 뜨고도 읽던 것은 나의 병(病)인가 청춘(靑春)의 병(病)인가? 하늘이 부끄러운 듯이 새빨개지고 바람이 이상스러운지 속살일 뿐이다.'}, {'id': 38, 'title': '비갠 아침', 'author': '이상화', 'year': 1926.0, 'tags': ['한글', '산문'], 'contents': '밤이 새도록 퍼붓던 그 비도 그치고\n동편 하늘이 이제야 불그레하다.\n기다리는 듯 고요한 이 땅 위로\n해는 점잖게 돋아 오른다.\n\n눈부시는 이 땅\n아름다운 이 땅\n내야 세상이 너무도 발고 깨끗해서\n발을 내밀기에 황송만 하다.\n\n해는 모든 것에서 젖을 주었나 보다.\n동무여, 보아라,\n우리의 앞뒤로 있는 모든 것이\n햇살의 가닥 ―― 가닥을 잡고 빨지 않느냐.\n\n이런 기쁨이 또 있으랴.\n이런 좋은 일이 또 있으랴.\n이 땅은 사랑 뭉텅이 같구나.\n아, 오늘의 우리 목숨은 복스러워도 보인다.'}, {'id': 39, 'title': '오감도', 'author': '이상', 'year': 1934.0, 'tags': ['복잡', '무형식'], 'contents': '十三人의兒孩가道路로疾走하오.\n(길은막달은골목이適當하오.)\n \n第一의兒孩가무섭다고그리오.\n第二의兒孩도무섭다고그리오.\n第三의兒孩도무섭다고그리오.\n第四의兒孩도무섭다고그리오.\n第五의兒孩도무섭다고그리오.\n第六의兒孩도무섭다고그리오.\n第七의兒孩도무섭다고그리오.\n第八의兒孩도무섭다고그리오.\n第九의兒孩도무섭다고그리오.\n第十의兒孩도무섭다고그리오.\n第十一의兒孩가무섭다고그리오.\n第十二의兒孩도무섭다고그리오.\n第十三의兒孩도무섭다고그리오.\n十三人의兒孩는무서운兒孩와무서워하는兒孩와그러케뿐이모혓소.(다른事情은업는것이차라리나앗소)\n\n그中에一人의兒孩가무서운兒孩라도좃소.\n그中에二人의兒孩가무서운兒孩라도좃소.\n그中에二人의兒孩가무서워하는兒孩라도좃소.\n그中에一人의兒孩가무서워하는兒孩라도좃소.\n\n(길은뚤닌골목이라도適當하오.)\n十三人의兒孩가道路로疾走하지아니하야도좃소.'}, {'id': 40, 'title': '수염', 'author': '이상', 'year': 1931.0, 'tags': ['복잡', '산문'], 'contents': '―(수·수·그밖에수염일수있는것들·모두를이름)\n\n이상\n\n\n 1\n눈이존재하여있지아니하면아니될처소는삼림인웃음이존재하여있었다\n\n 2\n홍당무\n\n 3\n아메리카의유령은수족관이지만대단히유려하다\n그것은음울하기도한것이다\n\n 4\n계류에서―\n건조한식물성이다\n가을\n\n 5\n일소대의군인이동서의방향으로전진하였다고하는것은\n무의미한일이아니면아니된다\n운동장이파열하고균열한따름이니까\n\n 6\n삼심원\n\n 7\n조(粟)를그득넣은밀가루포대\n간단한수유의월야이었다\n\n 8\n언제나도둑질할것만을계획하고있었다\n그렇지는아니하였다고한다면적어도구걸이기는하였다\n\n 9\n소한것은밀한것의상대이며또한\n평범한것은비범한것의상대이었다\n나의신경은창녀보다도더욱정숙한처녀를원하고있었다\n\n 10\n말(馬)―\n땀(汗)―\n                  ×\n여(余), 사무로써산보라하여도무방하도다\n여(余), 하늘의푸르름에지쳤노라이같이폐쇄주의로다\n\n1931.6.5'}, {'id': 41, 'title': '이상한 가역반응', 'author': '이상', 'year': 1931.0, 'tags': ['복잡', '무형식'], 'contents': '임의의반경의 圓(과거분사의 時勢)\n\n원내의일점과원외의일점을결부한직선\n\n이종류의존재의시간적경향성\n(우리들은이것에관하여무관심하다)\n\n\n直線은圓을殺害하였는가\n\n현미경\n그밑에있어서는인공도자연과다름없이현상되었다.\n          x\nX같은날의오후\n물론태양이존재하여있지아니하면아니될處所에존재하여있었을뿐만 아니라\n그렇게하지아니하면아니될步調를미화하는일까지도하지아니하고있었다.\n\n발달하지도아니하고발달하지도아니하고\n이것은憤怒이다.\n\n鐵柵밖의백대리석건축물이웅장하게 서있던\n眞眞5"의角바아의나열에서\n육체에대한처분을센티멘탈리즘하였다.\n\n목적이있지아니하였더니만큼冷靜하였다.\n\n태양이땀에젖은잔등을내려쬐었을 때\n그림자는잔등前方에있었다.\n\n사람은말하였다.\n\'저변비증환자는부자집으로식염을얻으려들어가고자희망하고 있는 것이다\'\n라고\n............\n'}, {'id': 42, 'title': '파편의 경치', 'author': '이상', 'year': 1931.0, 'tags': ['복잡', '산문'], 'contents': '―△은나의 AMOUREUSE이다\n\n이상\n\n\n나는하는수없이울었다\n\n전등이담배를피웠다\n▽은I/W이다\n            ×\n▽이여 ! 나는괴롭다\n나는유희한다\n▽의슬립퍼어는과자와같지아니하다\n어떻게나는울어야할것인가\n            ×\n쓸쓸한들판을생각하고\n쓸쓸한눈나리는날을생각하고\n나의피부를생각하지아니한다\n\n기억에대하여나는강체이다\n\n정말로\n「같이노래부르세요」\n나의무릎을때렸을터인일에대하여\n▽은나의꿈이다\n\n스틱크 ! 자네는쓸쓸하며유명하다\n\n어찌할것인가\n            ×\n마침내▽을매장한설경이었다\n\n1931.6.5'}, {'id': 43, 'title': '꽃나무', 'author': '이상', 'year': 1933.0, 'tags': ['한글', '산문'], 'contents': '벌판한복판에 꽃나무하나가있소. 근처(近處)에는꽃나무가하나도없소. 꽃나무는제가생각하는꽃나무를 열심(熱心)으로생각하는것처럼 열심으로꽃을피워가지고섰소. 꽃나무는제가생각하는꽃나무에게갈수없소. 나는막달아났소. 한꽃나무를위(爲)하여 그러는것처럼 나는참그런이상스러운흉내를내었소.'}, {'id': 44, 'title': '이런 시', 'author': '이상', 'year': 1933.0, 'tags': ['한글', '산문'], 'contents': '역사(役事)를하노라고 땅을파다가 커다란돌을하나 끄집어내여놓고보니 도모지어데서인가 본듯한생각이들게 모양이생겼는데 목도(木徒)들이 그것을메고나가드니 어데다갖다버리고온모양이길래 쫓아나가보니 위험(危險)하기짝이없는 큰길가드라.\n\n그날밤에 한소나기하얐으니 필시(必是)그돌이깨끗이씻겼을터인데 그이튿날가보니까 변괴(變怪)로다 간데온데없드라. 어떤돌이와서 그돌을업어갔을까 나는참이런처량(悽凉)한생각에서아래와같은작문(作文)을지였도다.\n\n「내가 그다지 사랑하든 그대여 내한평생(平生)에 차마 그대를 잊을수없소이다. 내차례에 못올사랑인줄은 알면서도 나혼자는 꾸준히생각하리다. 자그러면 내내어여쁘소서」\n\n어떤돌이 내얼골을 물끄러미 치여다보는것만같아서 이런시(詩)는그만찢어버리고싶드라'}, {'id': 45, 'title': '청령', 'author': '이상', 'year': 1940.0, 'tags': ['한글', '산문'], 'contents': '건드리면손끝에묻을듯이빨간봉선화\n너울너울하마날아오를듯하얀봉선화\n그리고어느틈엔가남으로고개를돌리는듯한일편단심의해바라기―\n이런꽃으로꾸며졌다는고흐의무덤은참얼마나아름다우리까.\n\n산은한낮에바라보아도\n비에젖은듯보얗습니다.\n\n포푸라는마을의지표(指標)와도같이\n실바람에도그뽑은듯헌출한키를\n포물선으로굽혀가면서진공과같이마알간대기속에서\n원경을축소하고있습니다.\n\n몸과나래도가벼운듯이잠자리가활동입니다.\n헌데그것은과연날고있는걸까요.\n흡사진공속에서라도날을법한데\n혹누가눈에보이지않는줄을이리저리당기는것이나아니겠나요.'}, {'id': 46, 'title': '한 개의 밤', 'author': '이상', 'year': 1940.0, 'tags': ['한글', '산문'], 'contents': '여울에서는도도한소리를치며\n비류강이흐르고있다.\n그수면에아른아른한자색층이어린다.\n\n십이봉봉우리로차단되어\n내가서성거리는훨씬뒤까지도이미황혼이깃들어있다.\n으스름한대기를누벼가듯이\n지하로지하로숨어버리는하류는검으틱틱한게퍽은싸늘하구나.\n\n십이봉사이로는\n빨갛게물든노을이바라보이고\n\n종이울린다.\n\n불행이여\n지금강변에황혼의그늘\n땅을길게뒤덮고도 오히려남을손불행이여\n소리날세라신방에창장을치듯\n눈을감는자나는 보잘것없이낙백한사람.\n\n이젠아주어두워들어왔구나\n십이봉사이사이로\n하마[1]별이하나둘모여들기시작아닐까\n나는그것을보려고하지않았을뿐\n차라리초원의어느한점을응시한다.\n\n문을닫은것처럼캄캄한색을띠운채\n이제비류강은무겁게도도사려앉는것같고\n내육신도천근\n주체할도리가없다.'}, {'id': 47, 'title': '습작 쇼윈도 수점', 'author': '이상', 'year': 1961.0, 'tags': ['한글', '산문'], 'contents': '북쪽을 향하고 남쪽으로 걷는 바람 속에 멈춰 선 부인\n영원의 젊은 처녀\n지구는 그녀와 서로 맞닿을듯이 자전한다.\n\n운명이란 것은\n사람들은 일만년후의 어느 일년 CALENDAR까지도 만들 수 있다.\n태양이여 달이여 종이 한장으로 된 CALENDAR여.\n\n달밤의 기권(氣圈)은 냉장고다.\n육체가 냉각한다. 혼백만이 월광만으로써 충분히 연소한다.'}, {'id': 48, 'title': '1931년', 'author': '이상', 'year': 1960.0, 'tags': ['복잡', '산문'], 'contents': '――작품제1번\n\n1\n\n나의 폐가 맹장염을앓다. 제4병원에입원. 주치의도난――망명의소문나다.\n철늦은나비를보다. 간호부인형매입. 모조맹장을제작하여한장의투명초자(硝子)의저편에대칭점을설치하다. 자택치료의묘(妙)를극하다.\n드디어 위병발병하여 안면창백. 빈혈.\n\n\n2\n\n심장거처불명. 위에있느니 가슴에있느니 이설(二說)분분하여걷잡을수없음.\n다량의출혈을보다. 혈액분석의결과 나의피는 무기질의혼합이라판명함.\n퇴원. 거대하며샤프한 기념비서다. 백색소년그전면(前面)에서협심증으로고꾸라지다.\n\n\n3\n\n나의안면에풀이자라났다. 이는불요불굴의미덕을상징한다.\n나는내자신을더도없이미워하여 등변형코오스의산보를매일이고계속했다.\n아니나다를까, 이는 1932년5월7일(부친의사일(死日))대리석발아사건의전조이었다.\n허나그때의나는아직한개의 방정식무기론(方程式無機論)의열렬한신봉자이었다.\n\n\n4\n\n뇌수교체문제 드디어중대화되다.\n나는남몰래 정충(精蟲)의일원론을고지(固持)하여 정충의유기질의분리실험에성공하다.\n유기질의무기화문제 남다.\nR청년공작(公爵)과해후하여 CREAM LEBRA의비밀을듣다. 그의소개에의하여이양(梨孃)과서로알다.\n예(例)의문제에 광명보이다.\n\n\n5\n\n혼혈아Y 나와의입맞춤에의하여독살되다.\n감금되다.\n\n\n6\n\n재차입원하다. 나는이렇게도암담한운명에직면하여자살을결의하고 남몰래 한자루의비수(길이3척)를손에넣었다.\n야음(夜陰)을타서나는병실에서빠져나왔다. 개가짖었다. 나는이때다싶어푹하고비수를나의배꼽에찔러넣었다.\n불행히도 나를체포하러추격하여온나의어머니가나의등에 나를감싸안은채살해되어있었다. 나는무사하였다.\n\n\n7\n\n지구의(地球儀)의위에물구나무 하였다는이유로나는제3인터내셔널당원들 에게뭇매에당했다\n그리고 조종사가없는비행기에태워진채공중에쏘아졌다. 혹형을맞았다.\n나는 지구의에접근한다. 지구의재정(財政)의뒷면을 이때엄밀자세히검산하는기회를얻었다\n\n\n8\n\n창부(娼婦)가분만한사아(死兒)의피부일면(一面)에 문신이입혀져있었다. 나는그암호를해제하였다.\n그사아의선조는 옛적에기관차를치어서그기관차로하여유혈임리(流血淋漓) 도망하게한일세(一世)의호걸이었다는것이기록되어있었다\n\n\n9\n\n나는제3개째의다리(脚) 제4개째의다리의설계중. 혁(爀)으로부터의「다리를자르다」의비보를접하고악연(愕然)함.\n\n\n10\n\n나의방의시계 돌연 13을치다. 그때호외의방울이울리다. 나의탈옥의기사.\n불면증과종안병(腫眼病)과로시달리고있는나는항상좌우의기로에섰다.\n나의내부로향하여 도덕의기념비가부수어지면서쓰러졌다. 중상(重傷). 세상은착오를전하다.\n13+1=12 이튿날(즉그때)부터나의시곗바늘은3개이었다.\n\n\n11\n\n삼차각(三次角)의여각(餘角)을발견하다. 다음으로 삼차각과삼차각의여각과의합은 삼차각과보각(補角)을이룸을발견하다.\n인구문제의응급수단 확정되다.\n\n\n12\n\n거울의굴절반사의법칙은시간방향유임(留任)문제를해결하다. (궤적의광년운산(運算))\n나는거울의수량을섬광의속도에의하여계산하였다. 거기에서 로켓트의설계를중단하였다.\n별보―이양 R청년공작가전(家傳)의발(簾)에감기어참사하다.\n별보―상형문자에의한사도(死都)발굴탐사대 그기관지로써성명서를발표하다.\n거울의불황과함께비관설대두하다.'}, {'id': 49, 'title': '내과', 'author': '이상', 'year': 1956.0, 'tags': ['복잡', '무형식'], 'contents': '――자가용복음――\n――혹은 엘리엘리라마사박다니――\n\n\n하이얀 천사\n이수염난천사는큐빗드의조부님이다.\n수염이전연(?)나지아니하는천사와 곧잘결혼하고는한다.\n\n나의늑골은2떠―즈(ㄴ). 하나하나에노크를하여본다. 그속에서는海綿에젖은더운물이끓고있다. 하이얀천사의펜네임은성피―타―라고. 고무의전선 똑똑똑똑 버글버글 열쇠구멍으로도청.\n\n(발신) 유다야인의임금님주무시나요?\n(반신) 찌―따찌―따따찌―찌―(1) 찌―따찌―따따찌―찌―(2) 찌―따찌―따따찌―찌―(3)\n\n흰뺑끼칠된십자가에서내가쑥쑥키가커진다. 성피―타―군이나에게세번이나알지못한다고그런다 그순간 닭이활개를친다......\n어엌 크 더운물을 엎질러서야 큰일 날 노릇――'}, {'id': 50, 'title': '최후', 'author': '이상', 'year': 1956.0, 'tags': ['일본어', '운문'], 'contents': '사과한알이떨어졌다. 지구는부서질정도로아팠다. 최후.\n이미여하한정신도발아하지아니한다.\n\n林檎一個が墜ちた。地球は壊れる程迄痛んだ。最後。\n最早如何なる精神も発芽しない。'}, {'id': 51, 'title': '정식', 'author': '이상', 'year': 1935.0, 'tags': ['복잡', '무형식'], 'contents': '   정식\n    I\n\n 해저에가라앉는한개닻처럼소도가그구간속에멸형하여버리더라완전히닳아없어졌을때완전히사망한한개소도가위치에유기되어있더라\n\n\n   정식\n    II\n\n 나와그알지못할험상궂은사람과나란히앉아뒤를보고있으면기상은몰수되어없고선조가느끼던시사의증거가최후의철의성질로두사람의교제를금하고있고가졌던농담의마지막순서를내어버리는이정돈한암흑가운데의분발은참비밀이다그러나오직그알지못할험상궂은사람은나의이런노력의기색을어떻게살펴알았는지그때문에그사람이아무것도모른다하여도나는또그때문에억지로근심하여야하고지상맨끝정리인데도깨끗이마음놓기참어렵다\n\n\n   정식\n    III\n\n 웃을수있는시간을가진표본두개골에근육이없다\n\n\n   정식\n    IV\n\n 너는누구냐그러나문밖에와서문을두드리며문을열라고외치니나를찾는일심이아니고또내가너를도무지모른다고한들나는차마그대로내어버려둘수는없어서문을열어주려하나문은안으로만고리가걸린것이아니라밖으로도너는모르게잠겨있으니안에서만열어주면무엇을하느냐너는누구기에구태여닫힌문앞에탄생하였느냐\n\n\n   정식\n    V\n\n 키가크고유쾌한수목이키작은자식을낳았다궤조가평편한곳에풍매식물의종자가떨어지지만냉담한배척이한결같아관목은초엽으로쇄약하고초엽은하향하고그밑에서청사는점점수척하여가고땀이흐르고머지않은곳에서수은이흔들리고숨어흐르는수맥에말뚝박는소리가들렸다\n\n\n   정식\n    VI\n\n 시계가뻐꾸기처럼뻐꾹거리길래쳐다보니목조뻐꾸기하나가와서모으로앉는다그럼저게울었을리도없고제법울까싶지도못하고그럼아까운뻐꾸기는날아갔나'}, {'id': 52, 'title': '1933, 6, 1', 'author': '이상', 'year': 1933.0, 'tags': ['한글'], 'contents': ' 천칭우에서 삼십년동안이나 살아온사람 (어떤과학자) 삼십만개나넘는 별을 다헤어놓고만 사람 (역시) 인간칠십 아니이십사년동안이나 뻔뻔히살아온 사람 (나)\n 나는 그날 나의자서전에 자필의부고를 삽입하였다 이후나의육신은 그런고향에는있지않았다 나는 자신나의시가 차압당하는꼴을 목도하기는 차마 어려웠기때문에.'}, {'id': 53, 'title': '선전대조', 'author': '이광수', 'year': 1942.0, 'tags': ['친일', '한글'], 'contents': "'미국과 영국을 쳐라'\n하옵신 대조를 나리시다\n12월 8일 해뜰 때\n빛나는 소화 16년\n\n하와이 진주만에\n적악을 때리는 황군의 첫 벽력\n\n웨스트 버어지니어와 오클라호마\n태평양 미함대 부서지다\n\n이어서 치는 남양의 해공육\n프린스.업.웨일즈 영함대 기함\n앵글의 죄악과 운명을 안고\n구안탄 바다 깊이 스러져 버리다.\n\n아시아의 성역은 원래\n천손 민족이 번영할 기업\n\n앵글의 발에 더럽힌 지 2백년\n우리 임금 이제 광복을 선하시다."}, {'id': 54, 'title': '조선의 학도여', 'author': '이광수', 'year': 1943.0, 'tags': ['친일', '한글'], 'contents': '그대는 벌써 지원하였는가,\n- 특별지원병을 -\n내일 지원하려는가\n- 특별지원병을 -\n\n공부야 언제나 못하리\n다른 일이야 이따가도 하지마는\n전쟁은 당장이로세\n만사는 승리를 얻은 다음날 일.\n\n승패의 결정은 지금으로부터.\n시각이 바쁜지라 학교도 쉬네.\n한 사람도 아쉬운지라 그대도 부르시네.\n1억이 모조리 전투배치에 서랍시는 오늘.\\\n\n그대는 벌써 뜻이 정하였으리,\n- 나가리이다, 나가 싸우리이다 -\n- 싸워서 이기리이다 -\n- 미영(米英)을 격멸하고 돌아오리이다 -\n조국의 흥망이 달린 이 결전민족의 운명이 결정되는 마루판\n단판일세, 다시 해볼 수 없는 끝판\n그대가 나가서 막을 마루판싸움\n\n아세아 10억 -\n칠 같은 머리\n흑보석 같은 눈\n황금색 살빛\n\n자비와 인과 맑은 마음과\n충과 효와 정렬(貞烈)과\n예의와 겸손과\n근면과 화평과,\n\n이러한 정신,\n이러한 문화,\n온유하고 순후한\n\n10억의 운명이 달린 결전.\n거룩한 우리 향토\n아세아의 성역을\n짓밟아 더럽히던,\n적을 쫓으라 - 하옵신 결전.\n\n이 싸움 이기고 나서\n아세아 사람의 아세아로\n천년의 태평이 있을 때\n그 어떤 문화가 필 것인가.\n아세아는 세계의 성전\n세계의 낙원, 이상향\n신앙과 윤리와 예술의 원천\n그러한 아세아를 세우려고\n맹수 독충을 몰아내는 성전(聖戰)\n일본 남아의 끓는 피로\n아세아의 해(海)와 육(陸)을\n깨끗이 씻어내는 성전\n\n- 이 성전의 용사로\n부름받은 그대 - 조선의 학도여\n- 지원하였는가, 하였는가\n- 특별지원병을 -\n그대, 무엇으로 주저하는가\n부모 때문인가\n충 없는 효 어디 있으리,\n\n그대 처자를 돌아보는가\n이 싸움 안 이기고 어디 있으리\n부모길래, 처자길래, 가라, 그대여,\n병역의 의무 없이도\n가는 그대의 의기(義氣) -\n그러므로 나라에서\n특별지원병이라 부르시도다.\n의무의 유무(有無)를 논하리,\n이 사정 저 형편 궁리하리,\n제만사(除萬事) 제잡담(除雜談)하고\n나서라 조선의 학도여\n\n그대들의 나섬은\n그대들의 충의(忠義), 가문의 영예,\n삼천만 조선인의 생광(生光)이오, 생로(生路),\n1억 국민의 기쁨과 감사.\n\n남아 한번 세상 나,\n이런 호기(好氣) 또 있던가,\n일생일사(一生一死)는 저마다 다 있는 것,\n위국충절은 그대만의 행운\n\n가라 조선의 6천 학도여,\n삼천만 동향인(同鄕人)의 앞잡이 되라,\n총후(銃後)의 국민의 큰 기탁(寄託)과\n누이들의 만인침(萬人針)을 받아 띠고 가라'}, {'id': 55, 'title': '새해', 'author': '이광수', 'year': 1944.0, 'tags': ['친일', '한글'], 'contents': '새해가 왔네\n지구가 처음 보는 위대한 새해, 탐욕의 지옥인 구세계가 문허지고\n仁義(인의)와 예의 새 세계의 터를 닥는 새해\n태평양의 물결에 잔잔함이 돌아오고 아세아의 천지에 흥복의 만세소리가 우렁차게\n일어날 새해\n\n깃버라. 나는 이 새해를 보앗서라, 개벽이래 처음오는 위대하나 새해를 노래하는\n나의 행운이어\n그러나 1억의 동포여\n이 해 새해는 또 땀을 만히 흘려야할래\n\n농부는 논밧을 갈기에, 갓구기에, 일구기에, 광부는 땅속에서 파기에, 깨트리기에,\n저내이기에\n工夫(공부)는 공장에서 갈기에, 두들기기에, 어부는 바다에서 그물치기에, 낙기에, 끌기에, 남,녀,노,소 一億一心(일억일심), 쉬일새업시 흘리는 땀이 일본의 국토를 흠씬 적실때에 ― 오직 그때에만야\n영광의 승리는 오는 것이다. 이를 일러 1억 전투배치,전력증강\n빛나는 새해 위대한 새해\n\n씩씩한 우리 아들들은 총을 메고 전장으로 나가고\n어엿븐 우리 딸들은 몸빼를 입고 공장으로 농장으로 나서네\n말모르는 마소까지도 나라일 위해 나서는 오늘이 아닌가\n천년 평화 道義(도의) 세계를 세우랍신\n\n우리 임금님의 명을 밧자와\n"예", "예" 하고 집에서 뛰어나오는 무리\n이날 설날에 반도3천리도 기쁨의 일장기바다.\n무한한 영광과 희망의 위대한 새해여.'}, {'id': 56, 'title': '간', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '운문'], 'contents': '바닷가 햇빛 바른 바위 우에\n습한 간(肝)을 펴서 말리우자\n\n코카사쓰 산중에서 도망해 온 토끼처럼\n둘러리를 빙빙 돌며 간을 지키자\n\n내가 오래 기르든 여윈 독수리야!\n와서 뜯어 먹어라 시름없이\n\n너는 살지고\n나는 여위어야지, 그러나\n\n거북이야!\n다시는 용궁의 유혹에 안 떨어진다.\n\n프로메테우스, 불쌍한 프로메테우스\n불 도적한 죄로 목에 맷돌을 달고\n끝없이 침전하는 프로메테우스.'}, {'id': 57, 'title': '또 태초의 아침', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '운문'], 'contents': '하얗게 눈이 덮이었고\n전신주(電信柱)가 잉잉 울어\n하나님 말씀이 들려온다.\n무슨 계시(啓示)일까.\n빨리\n봄이 오면\n죄(罪)를 짓고\n눈이\n밝아\n이브가 해산(解産)하는 수고를 다하면\n무화과(無花果) 잎사귀로 부끄런 데를 가리고\n나는 이마에 땀을 흘려야겠다.'}, {'id': 58, 'title': '소년', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '산문'], 'contents': '여기저기서 단풍잎 같은 슬픈 가을이 뚝뚝 떨어진다. 단풍잎 떨어져 나온 자리마다 봄을 마련해 놓고 나뭇가지 우에 하늘이 펼쳐 있다. 가만히 하늘을 들여다보려면 눈썹에 파란 물감이 든다. 두 손으로 따뜻한 볼을 쓸어보면 손바닥에도 파란 물감이 묻어난다. 다시 손바닥을 들여다본다. 손금에는 맑은 강물이 흐르고, 맑은 강물이 흐르고, 강물 속에는 사랑처럼 슬픈 얼굴----아름다운 순이(順伊)의 얼굴이 어린다. 소년은 황홀히 눈을 감아 본다. 그래도 맑은 강물은 흘러 사랑처럼 슬픈 얼굴-----아름다운 순이(順伊)의 얼굴은 어린다.'}, {'id': 59, 'title': '새로운 길', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '운문', '반복'], 'contents': '내를 건너서 숲으로\n고개를 넘어서 마을로\n\n어제도 가고 오늘도 갈\n나의 길 새로운 길\n\n민들레가 피고 까치가 날고\n아가씨가 지나고 바람이 일고\n\n나의 길은 언제나 새로운 길\n오늘도...... 내일도 ......\n\n내를 건너서 숲으로\n고개를 넘어서 마을로'}, {'id': 60, 'title': '슬픈 족속', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '운문'], 'contents': '흰 수건이 검은 머리를 두르고\n흰 고무신이 거친 발에 걸리우다\n\n흰 저고리 치마가 슬픈 몸집을 가리고\n흰 띠가 가는 허리를 질끈 동이다'}, {'id': 61, 'title': '십자가', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '반복', '운문'], 'contents': '쫓아오든 햇빛인데\n지금 교회당 꼭대기\n십자가에 걸리었습니다.\n\n첨탑(尖塔)이 저렇게도 높은데\n어떻게 올라갈 수 있을까요.\n\n종소리도 들려 오지 않는데\n휘파람이나 불며 서성거리다가\n\n괴로웠던 사나이\n행복한 예수 그리스도에게\n처럼\n십자가가 허락된다면\n\n모가지를 드리우고\n꽃처럼 피어나는 피를\n어두워 가는 하늘 밑에\n조용히 흘리겠습니다.'}, {'id': 62, 'title': '새벽이 올 때까지', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '운문'], 'contents': '다들 죽어가는 사람들에게\n검은 옷을 입히시오.\n\n다들 살아가는 사람들에게\n흰 옷을 입히시오.\n\n그리고 한 침실(寢室)에\n가지런히 잠을 재우시오\n\n다들 울거들랑\n젖을 먹이시오\n\n이제 새벽이 오면\n나팔소리 들려 올 게외다.'}, {'id': 63, 'title': '길', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '반복', '운문'], 'contents': '잃어버렸습니다.\n무얼 어디다 잃었는지 몰라\n두 손의 호주머니를 더듬어\n길에 나갑니다.\n\n돌과 돌과 돌이 끝없이 연달아\n길은 돌담을 끼고 갑니다.\n\n담은 쇠문을 굳게 담아\n길 위에 긴 그림자를 드리우고\n\n길은 아침에서 저녁으로\n저녁에서 아침으로 통했습니다.\n\n돌담을 더듬어 눈물짓다\n쳐다 보면 하늘은 부끄럽게 푸릅니다.\n\n풀 한 포기 없는 이 길을 걷는 것은\n담 저 쪽에 내가 남아 있는 까닭이고\n\n내가 사는 것은 다만\n잃은 것을 찾는 까닭입니다. (1941.9.31)'}, {'id': 64, 'title': '병원', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '산문'], 'contents': '살구나무 그늘로 얼굴을 가리고, 병원 뒤뜰에 누워, 젊은 여자가 흰 옷 아래로 하얀 다리를 드러내 놓고 일광욕을 한다. 한나절이 기울도록 가슴을 앓는 다는 이 여자를 찾아오는 이, 나비 한 마리도 없다. 슬프지도 않은 살구나무 가지에는 바람조차 없다.\n\n나도 모를 아픔을 오래 참다 처음으로 이 곳에 찾아 왔다. 그러나 나의 늙은 의사는 젊은이의 병을 모른다. 나한테는 병이 없다고 한다. 이 지나친 시련, 이 지나친 피로, 나는 성내서는 안 된다.\n\n여자는 자리에서 일어나 옷깃을 여미고 화단에서 금잔화 한 포기를 따 가슴에 꽂고 병실 안으로 사라진다. 나는 그 여자의 건강이 ---- 아니 내 건강도 속히 회복되기를 바라며 그가 누웠던 자리에 누워 본다.'}, {'id': 65, 'title': '또 다른 고향', 'author': '윤동주', 'year': 1941.0, 'tags': ['한글', '반복', '운문'], 'contents': '고향에 돌아온 날 밤에\n내 백골(白骨)이 따라와 한방에 누웠다.\n\n어둔 방은 우주로 통하고\n하늘에선가 소리처럼 바람이 불어 온다.\n\n어둠 속에 곱게 풍화작용(風化作用)하는\n백골을 들여다보며\n눈물짓는 것이 내가 우는 것이냐\n백골이 우는 것이냐\n아름다운 혼이 우는 것이냐\n\n지조 높은 개는\n밤을 새워 어둠을 짖는다.\n어둠을 짖는 개는\n나를 쫓는 것일 게다.\n\n가자 가자\n쫓기우는 사람처럼 가자\n백골 몰래\n아름다운 또 다른 고향에 가자.'}, {'id': 66, 'title': '조개껍데기', 'author': '오장환', 'year': 1933.0, 'tags': ['한자', '운문'], 'contents': '대글대글\n◇\n색동저고리 떨치인 조개껍데기\n◇\n潮水가물너슨 砂邊에 日光浴을하오.'}, {'id': 67, 'title': '아침', 'author': '오장환', 'year': 1933.0, 'tags': ['한글', '운문'], 'contents': '까마귀 한 마리\n게을리 노래하며\n감나무에 앉었다.\n\n자숫물 그릇엔\n어름덩이 둘'}, {'id': 68, 'title': '화염', 'author': '오장환', 'year': 1933.0, 'tags': ['한글', '한자', '운문'], 'contents': '한낮에 불이야!\n恍惚한 消防手 나러든다\n\n滿開한 薔薇에 虎蝶'}, {'id': 69, 'title': '버러지도 싫다하올 이 몸이', 'author': '변영로', 'year': 1924.0, 'tags': ['한글', '운문'], 'contents': '1\n버러지도 싫다하올 이몸이\n불현듯 그대 생각 어인 일가\n그리운 마음 자랑스럽습니다.\n\n촛불 밝고 마음 어둔 이밤에\n당신 어디 계신지 알길 없어\n답답함에 이내 가슴 터집니다.\n\n2\n철 안나 복스럽던 옛날엔\n그대와 나 한 동산에 놀았지요\n그 때는 꽃빛도 더 짙었읍니다.\n\n언젠가 우리 둘이 강가에 놀 때\n날으던 것은 흰 새였건만은\n모래 위 그림자는 붉었읍니다.\n\n바로 그 때 난데없는 바람 일어\n그대와 나의 어린 눈 흐리워져\n얼결에 서로 손목 쥐었읍니다.\n\n3\n그러나 바람이 우리를 시기하였던가\n바람은 나뉘어 불지 아니하였으련만\n찢기이는 옷 같이 우리는 갈렸읍니다.\n\n이제 것도 그리움이 눈 흐리울 때\n길에서 그대같은 이 보건만은\n아니실 줄 알고 눈 감고 곁길로 가옵니다.'}, {'id': 70, 'title': '곤충구제', 'author': '변영로', 'year': 1941.0, 'tags': ['한글', '운문'], 'contents': '자벌레\n\n숲속에 홀로 누워 내 보았다 유심히도\n치(寸[촌]) 올라 자(尺[척]) 나리되 쉬지 않는 자\n벌레를\n나무야 오르든 마든 그 뜻 못내 부러라.\n\n딱정벌레\n\n노란 등 깜은 斑點 迷彩[반점 미채]일시 분명하다\n꽂빛과 잎그늘에 아롱아롱 섞일러니\n재바른 새 눈에 띄어 온데 간데 없더라.\n\n반 딧 불\n\n칠같이 검은 밤에 켰다 껐다 작은 燈[등]불\n풀잎에 걸리일까 妖精[요정]의 길 밝힘인가\n그 불빛 따라만 가면 시름 뇔가 하노라.\n\n말똥구리\n\n여름날 村[촌]길 우에 그악쟁이 말똥구리\n잠신들 제 일 쉬움 어느 뉘 보았는다\n이름이 점잖지 않다 낮보지는 못하리.\n\n불 나 비\n\n어둠에 쫓긴 나비 불빛 찾어 날아들어\n깃만을 태우던가 몸마저 사루우네\n어두면 어두운대로 살아보면 어떠리.\n\n오줌쌔기\n\n머리는 적은 것이 허리춤은 기단 것이\n제 무엇 믿기관대 앞발 세고 일어선다\n바퀴야 저의 威嚴[위엄]쯤 안 적이나 있으랴\n\n베 짱 이\n\n풀빛 베짱이 풀잎에 매달리어\n찌르르 울을제에 난데없는 涼味[양미] 돈다\n처마 끝 발 들이니 시원 더욱 하고나\n\n門閣民[문각씨]\n\n누구의 죽은 넋이 門閣民[문각씨]로 태어나서\n秋夜長[추야장] 긴 긴 밤에 남의 心思[심사] 흔드는다\n밤중만 도드락 소래에 잠 못이뤄 하노라\n\n소금쟁이\n\n물우에 성큼성큼 長脚 濶步[장각 활보] 소금쟁이\n진종일 물에 살되 물 한방울 적실소냐\n어즈버 渡世[도세]하는 법 네게 밸가 하노라.\n\n개 고 리\n\n혼자냥 池塘[지당]가를 低頭 沈思[저두 침사] 거닐러니\n무삼것 뛰어들어 鏡水 無風[경수 무풍] 소래 내네\n어이타 경망한 개고리 나의 꿈을 깨는다.\n\n개고리 저 개고리 水陸 自在[수륙 자재] 부러우나\n붙은 발 굽은 등에 腹背[복배] 빛갈 다른데다\n솟긴 눈 이저리 굴려 믿긴 어려 하노라.\n─「文章[문장]」, 1941. 4'}, {'id': 71, 'title': '가을 하늘 밑에 서서', 'author': '변영로', 'year': 1922.0, 'tags': ['한글', '반복', '운문'], 'contents': '엄청나게 높고 깊은 하늘.\n어이없이도 새파란 가을 하늘 !\n끔찍한 당신의 天幕[천막]이로소이다\n오오, 님이시여!\n\n끔찍하게 넓은 하늘― 당신의 天幕[천막] !\n만유는 다 당신의 軍隊[군대]로소이다\n왕도 거지도 바다도 산도,\n오오, 님이시여 !'}, {'id': 72, 'title': '봄비', 'author': '변영로', 'year': 1922.0, 'tags': ['한글', '반복'], 'contents': '나즉하고 그윽하게 부르는 소리 있어\n나아가 보니, 아, 나아가 보니\n졸음 잔뜩 실은 듯한 젖빛 구름만이\n무척이나 가쁜 듯이, 한없이 게으르게\n푸른 하늘 위를 거닌다.\n아, 잃은 것 없이 서운한 나의 마음!\n\n나즉하고 그윽하게 부르는 소리 있어\n나아가 보니, 아, 나아가 보니\n아렴풋이 나는 지난날의 회상같이\n떨리는 뵈지 않는 꽃의 입김만이\n그의 향기로운 자랑 앞에 자지러지노라!\n아, 찔림 없이 아픈 나의 가슴!\n\n나즉하고 그윽하게 부르는 소리 있어\n나아가 보니, 아, 나아가 보니\n이제는 젖빛 구름도 꽃의 입김도 자취 없고\n다만 비둘기 발목만 붉히는 은실 같은 봄비만이\n소리도 없이 근심같이 나리누나!\n아, 안 올 사람 기다리는 나의 마음!'}, {'id': 73, 'title': '싸늘한 이마', 'author': '박용철', 'year': 1930.0, 'tags': ['한글', '반복', '운문'], 'contents': '큰 어둠 가운데 홀로 밝은 불 켜고 앉아 있으면 모두 빼앗기는 듯한 외로움\n한 포기 산꽃이라도 있으면 얼마나한 위로이랴\n모두 빼앗기는 듯 눈덮개 고이 나리면 환한 온몸은 새파란 불 붙어 있는 인광(燐光)\n까만 귀뚜리 하나라도 있으면 얼마나한 기쁨이랴\n파란 불에 몸을 사루면 싸늘한 이마 맑게 트이여 기어가는 신경의 간지러움\n길잃은 별이라도 맘에 있다면 얼마나 한 즐검이랴'}, {'id': 74, 'title': '젊은이들에게', 'author': '노천명', 'year': 1942.0, 'tags': ['한글', '산문', '친일'], 'contents': '늙은 영국을 대해서\n저 혼혈아 아메리카를 향해서\n제국(帝國)은 드디어 선전을 포고했다\n정의를 위해 대동아건설을 위해서\n우리는 불수레를 달렸다'}, {'id': 75, 'title': '군신송', 'author': '노천명', 'year': 1944.0, 'tags': ['한글', '친일', '산문'], 'contents': '이 아침에도 대일본 특공대는\n남방 거친 파도 위에\n혜성 모양 장엄하게 떨어졌으리\n\n싸움하는 나라의 거리다운\n네거리를 지나며\n12월의 하늘을 우러러 본다\n\n어뢰를 안고 몸으로\n적기를 부순 용사들의 얼굴이\n하늘가에 장미처럼 핀다\n성좌처럼 솟는다'}, {'id': 76, 'title': '*기원', 'author': '노천명', 'year': 1942.0, 'tags': ['한글', '친일'], 'contents': '신사(神社)의 이른 아침\n뜰엔 비질한 자욱 머리빗은 듯 아직 새로운데\n경건(敬虔)히 나와 손 모으며 기원하는 여인이 있다.\n\n일본의 전 아세아의 무운을 비는 청정한 아침이어라\n\n어머니의 거룩한 정성\n아내의 간절한 기원\n아버지를 위한 갸륵한 마음들……\n같은 이 시간 방방곡곡 신사가 있는 곳\n아름다운 이런 정경이 빚어지고 있으리'}, {'id': 77, 'title': '부인근로대', 'author': '노천명', 'year': 1942.0, 'tags': ['한글', '친일'], 'contents': '부인근로대 작업장으로\n군복을 지으러 나온 여인들\n머리엔 흰 수건 아미 숙이고\n바쁘게 나르는 흰 손길은 나비인가\n\n총알에 맞아 뚫어진 자리\n손으로 만지며 기우려 하니\n탄환을 맞던 광경 머리에 떠올라\n뜨거운 눈물이 피잉 도네\n\n한 땀 두땀 무운을 빌며\n바늘을 옮기는 양 든든도 하다\n일본의 명예를 걸고 나간 이여\n훌륭히 싸워주 공을 세워주\n\n나라를 생각하는 누나와 어머니의 아름다운 정성은\n오늘도 산만한 군복 위에 꽃으로 피었네'}, {'id': 78, 'title': '별의 압흠', 'author': '남궁벽', 'year': 1922.0, 'tags': ['한글', '반복', '운문'], 'contents': '임이시여, 나의 임이시여, 당신은\n어린 아이가 뒹굴을 때에\n감응적으로 깜짝 놀라신 일이 없으십니까.\n\n임이시여, 나의 임이시여, 당신은\n세상 사람들이 지상의 꽃을 비틀어 꺾을 때에\n천상의 별이 아파한다고는 생각지 않으십니까.'}, {'id': 79, 'title': '오월', 'author': '김영랑', 'year': 1935.0, 'tags': ['한글', '운문'], 'contents': '들길은 마을에 들자 붉어지고\n마을 골목은 들로 내려서자 푸르러졌다\n바람은 넘실 천이랑 만이랑\n이랑이랑 햇빛이 갈라지고\n보리도 허리통이 부끄럽게 드러났다\n꾀꼬리도 엽태 혼자 날아볼 줄 모르나니\n암컷이라 쫓길 뿐\n숫놈이라 쫓을 뿐\n황금 빛난 길이 어지럴 뿐\n얇은 단장하고 아양 가득 차 있는\n산봉우리야 오늘밤 너 어디로 가버리련'}, {'id': 80, 'title': '가늘한 내음', 'author': '김영랑', 'year': 1935.0, 'tags': ['한글', '운문'], 'contents': '내 가슴 속에 가늘한 내음\n애끈히 떠도는 내음\n저녁해 고요히 지는 제\n먼산 허리에 슬리는 보랏빛\n\n오! 그 수심띤 보랏빛\n내가 잃은 마음의 그림자\n한 이틀 정열에 뚝뚝 떨어진 모란의\n깃든 향취가 이 가슴 놓고 갔을 줄이야\n\n얼결에 여읜 봄 흐르는 마음\n헛되이 찾으려 허덕이는 날\n뻘 위에 철-석 갯물이 놓이듯\n얼컥 이-는 후끈한 내음\n\n아! 후끈한 내음 내키다 마는\n서어한 가슴에 그늘이 도나니\n수심 뜨고 애끈하고 고요하기\n산허리에 슬리는 저녁 보랏빛'}, {'id': 81, 'title': '그 색시 서럽다', 'author': '김영랑', 'year': 1935.0, 'tags': ['한글', '운문'], 'contents': '그 색시 서럽다 그 얼굴 그 동자가\n가을 하늘가에 도는 바람슷긴 구름조각\n핼쑥하고 서느라와 어디로 떠갔으랴\n그 색시 서럽다 옛날의 옛날의'}, {'id': 82, 'title': '내 마음을 아실 이', 'author': '김영랑', 'year': 1935.0, 'tags': ['한글', '운문'], 'contents': '내 마음을 아실 이\n내 혼자 마음 날같이 아실 이\n그래도 어데나 계실 것이면\n\n내 마음에 때때로 어리우는 티끌과\n속임 없는 눈물의 간곡한 방울방울\n푸른 밤 고이 맺는 이슬 같은 보람을\n보밴 듯 감추었다 내어드리지\n\n아! 그립다\n내 혼자 마음 날같이 아실 이\n꿈에나 아득히 보이는가\n\n향 맑은 옥돌에 불이 달아\n사랑은 타기도 하오련만\n불빛에 연긴 듯 희미론 마음은\n사랑도 모르리 내 혼자 마음은'}, {'id': 83, 'title': '*님 따라 나서자', 'author': '김억', 'year': 1944.0, 'tags': ['한글', '친일'], 'contents': '역천(逆天)을 부를것이 순천(順天)을 받들것이\n대장부 세상났다가 그저 옐줄 있는다.\n\n이목숨 귀할시고 모두들 아낀다면\n일월(日月)의 충의도고는 보잘것이 있는고.\n\n설사(設使)에 죽어라도 충혼(忠魂)은 그저남아\n사악을 눕히기전야 가실줄이 있과저.\n\n신풍(新風)이 부는고야 육탄이 튀는고야\n풍탄(風彈)이 튀는곳에 거칠것이 없나니.\n\n맘들을 한데모아 역천(逆天)을 부서지고\n님따라 손높이들고 나설때는 왔나니.\n라이선스[편집]'}, {'id': 84, 'title': '기도', 'author': '김억', 'year': 1925.0, 'tags': ['한글', '산문'], 'contents': '구하면 주지 못할 것이 없는 ‘우주’의 저자(著者)시여,\n팔을 팔배 하면 팔십팔 되게 하시는 전능자(全能者)시여,\n어제 이 죄인이 장에 갔다가, ‘우정’이란 괴물을\n술 한 잔으로 사서 죄인의 소유를 만들었습니다,\n만은 오늘은 술 한 잔 값이 없어 그것을 잃었습니다.\n어찌나 죄인 맘이 섧고 서어하겠습니까―\n간절히 비옵나니, 잃어버린 ‘우정’이란 그 괴물을\n아무쪼록 다시 찾아서 죄인의 것을 만들어 주소서.\n구하면 주지 못할 것이 없는 ‘우주’의 저자시여.'}, {'id': 85, 'title': '기억', 'author': '김억', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '그러하다, 인생은 기억, 기억은 잔회(殘灰)의\n쓸 데도 없는 지나간 꿈은 지금 와서\n나의 불서러운 이 몸을 붙잡고\n이리도 괴롭히며, 이리도 아프게 하여라.\n\n그러하나, 지금 나의 이 몸에 매달려,\n그윽하게도 삼가는 듯하게도\n저, 지나간 옛날의 한때의 꿈은\n흐득여 울며, 나더러 돌아가라 하여라.\n\n그러면 나는 이르노니,\n인생은 꿈, 꿈은 망각의 바다에서\n스러져 자취조차 없어질 그것이라고,\n가을 지고, 겨울 와서 해조차 바뀌는 때의.'}, {'id': 86, 'title': '기억은 죽지도 않는가', 'author': '김억', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '얼을 뽑아내는 낙열(樂悅)의\n썩 깊은 악곡(樂曲)에도 오히려 ‘외로움’은\n쉬지 않고 삼가는 발소리로 머릿속을 오가나니,\n아아 이는 그대를 잃은 옛 조기(調記)인가.\n\n문득스럽게도 생겨난 사랑과 기쁨의\n문득스럽게도 자취도 없이 쓰러져 없어진,\n바람결에 좇아다니는, 그 기억의 곡조는\n때의 봄철, 흐르는 강물과도 같게,\n아양스럽게도, 애처롭게도 살뜰하게도\n또다시 지나간 ‘맘’을 붙잡고 흐득이나니,\n아아 이는 그대를 잃은 옛 곡조런가.\n\n만일에 이 곡조를 설은 기억이라면\n설은 기억의 곡조는 죽을 줄도 모르는가.'}, {'id': 87, 'title': '낭인의 봄', 'author': '김소월', 'year': 1920.0, 'tags': ['한글', '반복', '운문'], 'contents': '휘둘니 산을 넘고,\n굽어진 물을 건너,\n푸른 풀 붉은 꽃에\n길 걷기 시름이여.\n\n잎 누른 시닥나무,\n철 이른 푸른 버들,\n해 벌서 석양인데\n불슷는 바람이여.\n\n골짜기 이는 연기\n뫼 틈에 잠기는데,\n산마루 도는 손의\n슬지는 그림자여.\n\n산길가의 외론 주막,\n에이그 쓸쓸한데.\n먼저 든 짐장사의\n곤(困)한 말 한 소리여.\n\n지는 해 그림지니,\n오늘은 어데까지,\n어둔 뒤 아무데나,\n가다가 묵을레라.\n\n풀숲에 물김 뜨고,\n달빛에 새 놀래는,\n고운 밤 야반(夜半)에도\n내 사람 생각이여.'}, {'id': 88, 'title': '가을 저녁에', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '물은 희고 길구나, 하늘보다도.\n구름은 붉구나, 해보다도.\n서럽다, 높아 가는 긴 들 끝에\n나는 떠돌며 울며 생각한다, 그대를.\n\n그늘 깊이 오르는 발 앞으로\n끝없이 나아가는 길은 앞으로.\n키 높은 나무 아래로, 물 마을은\n성긋한 가지가지 새로 떠오른다.\n\n그 누가 온다고 한 言約도 없건마는!\n기다려 볼 사람도 없건마는!\n나는 오히려 못 물가를 싸고 떠돈다.\n그 못물로는 놀이 잦을 때.'}, {'id': 89, 'title': '고독', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '설움의 바닷가의\n모래밭이라\n침묵의 하루 해만 또 저물었네\n\n탄식의 바닷가의\n모래밭이니\n꼭 같은 열두 시만 늘 저무누나\n\n바잽의 모래밭에\n돋는 봄풀은\n매일 붓는 범불에 터도 나타나\n설움의 바닷가의\n모래밭은요\n봄 와도 봄 온줄을 모른다더라\n\n이즘의 바닷가의 모래밭이면\n오늘도 지는 해니 어서 져다오\n\n아쉬움의 바닷가 모래밭이니\n뚝 씻는 물소리가 들려나다오'}, {'id': 90, 'title': '삼수갑산 - 차안서삼갑산운', 'author': '김소월', 'year': 1934.0, 'tags': ['한글', '반복', '운문'], 'contents': '삼수갑산(三水甲山) 내 왜 왔노 삼수갑산이 어디뇨\n오고나니 기험(寄險)타 아하 물도 많고 산첩첩(山疊疊)이라 아하하\n\n내 고향을 도로 가자 내 고향을 내 못 가네\n삼수갑산 멀드라 아하 촉도지난(蜀道之難)이 예로구나 아하하\n\n삼수갑산이 어디뇨 내가 오고 내 못 가네\n불귀(不歸)로다 내 고향 아하 새가 되면 떠가리라 아하하\n\n님 계신 곳 내 고향을 내 못 가네 내 못 가네\n오다 가다 야속타 아하 삼수갑산이 날 가두었네 아하하\n\n내 고향을 가고지고 오호 삼수갑산 날 가두었네\n불귀로다 내 몸이야 아하 삼수갑산 못 벗어난다 아하하'}, {'id': 91, 'title': '박넝쿨타령', 'author': '김소월', 'year': 1939.0, 'tags': ['한글', '산문'], 'contents': '박넝쿨이 에헤이요 벋을적만 같아선\n온세상을 얼사쿠나 다뒤덮는것같더니\n하드니만 에헤이요 에헤이요 에헤야\n草家집 三門을 못덮었네, 에헤이요 못덮었네.\n\n복송아꽃이 에헤이요 피일적만 같아선\n봄동산을 얼사쿠나 도맡아놀것 같더니\n하드니만 에헤이요 에헤이요 에헤야\n나비한마리도 못붙잡데, 에헤이요 못붙잡데.\n\n박넝쿨이 에헤이요 벋을적만 같아선\n가을올줄을 얼사쿠나 아는이가적드니\n얼사쿠나 에헤이요 둥근박만 달렸네.'}, {'id': 92, 'title': '봄 못', 'author': '김소월', 'year': 1926.0, 'tags': ['한글', '운문'], 'contents': '같은 봄은 왔다니\n잎만 수북 떠 있다\n헐고 외인 못물가\n내가 서서 볼 때다.\n\n물에 드는 그림자\n어울리며 흔든다\n세도 못할 물소용\n물 면드로 솟군다.\n\n채 솟구도 못하여\n솟구다는 삼킨다.\n하건대는 우리도\n이러하다 할소냐.\n\n바람 앞에 풍겨나\n제자리를 못 잡아\n몸을 한곳 못 두어\n애가 탈손 못물아.\n\n한때 한때 지나다\n가고말 것뿐이라\n다시 헌된 세상에\n안정 밖에 있겠나.'}, {'id': 93, 'title': '옷과 밥과 자유', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '공중(空中)에 떠다니는\n저기 저 새여\n네 몸에는 털 있고 깃이 있지.\n\n밭에는 밭곡식\n논에는 물벼\n눌하게 익어서 수그러졌네.\n\n초산(楚山) 지나 적유령(狄踰嶺)\n넘어선다.\n짐 실은 저 나귀는 너 왜 넘니?'}, {'id': 94, 'title': '공원의 밤', 'author': '김소월', 'year': 1922.0, 'tags': ['한글', '운문'], 'contents': '백양가지에 우는 접동은 깊은 밤의 못물에\n어렷하기도 하며 아득하기도 하여라.\n어둡게 또는 소리없이 가늘게\n줄줄의 버드나무에서는 비가 쌓일때.\n\n푸른 하늘은 고요히 내려 갈리던 그 보드러운 눈결!\n이제, 검은 내는 떠돌아오라 비구름이 되어라.\n아아 나는 우노라 그 옛적의 내사람!'}, {'id': 95, 'title': '가막덤불', 'author': '김소월', 'year': 1922.0, 'tags': ['한글', '운문'], 'contents': '산에 가시나무\n가막 덤불은\n덤불 덤불 산마루로\n벌어 올랐소\n\n산에는 가려해도\n가지 못하고\n바로 말로\n집도 있는 내 몸이라오\n\n길에는 혼잣몸의\n홑옷 자락은\n하룻밤 눈물에는\n젖기도 했소\n\n산에는 가시나무\n가막덤불은\n덤불덤불 산마루로\n벌어 올랐소.'}, {'id': 96, 'title': '진달래꽃', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '진달래꽃\n\n나 보기가 역겨워\n가실 때에는\n말없이 고이 보내드리오리다.\n\n영변(寧邊)에 약산(藥山)\n진달래꽃\n아름따다 가실 길에 뿌리오리다.\n\n가시는 걸음 걸음\n놓인 그 꽃을\n사뿐히 즈려밟고 가시옵소서.\n\n나 보기가 역겨워\n가실 때에는\n죽어도 아니 눈물 흘리오리다.'}, {'id': 97, 'title': '집생각', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': "산(山)에나 올라서서\n바다를 보라\n사면(四面)에 백(百)열리(里), 창파(滄波) 중에\n객선(客船)만 중중…… 떠나간다.\n\n명산대찰(名山大刹)이 그 어디메냐\n향안(香案), 향합(香盒), 대그릇에,\n석양(夕陽)이 산(山)머리 넘어가고\n사면(四面)에 백(百)열리(里), 물소리라\n\n'젊어서 꽃같은 오늘날로\n금의(錦衣)로 환고향(還故鄕) 하옵소사.'\n객선(客船)만 중중…… 떠나간다\n사면(四面)에 백(百)열리(里), 나 어찌갈까\n\n까투리도 산(山)속에 새끼치고\n타관만리(他關萬里)에 와 있노라고\n산(山)중만 바라보며 목메인다\n눈물이 앞을 가리운다고\n\n들에나 나려오면\n치어다보라\n해님과 달님이 넘나든 고개\n구름만 첩첩…… 떠돌아간다"}, {'id': 98, 'title': '풀 따기', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '우리 집 뒷산에는 풀이 푸르고\n숲 사이의 시냇물, 모래 바닥은\n파아란 풀 그림자, 떠서 흘러요.\n\n그립은 우리 님은 어디 계신고.\n날마다 피어나는 우리 님 생각.\n날마다 뒷산에 홀로 앉아서\n날마다 풀을 따서 물에 던져요.\n\n흘러가는 시내의 물에 흘러서\n내어던진 풀잎은 옅게 떠갈 제\n물살이 헤적헤적 품을 헤쳐요.\n\n그립은 우리 님은 어디 계신고.\n가여운 이 내 속을 둘 곳 없어서\n날마다 풀을 따서 물에 던지고\n흘러가는 잎이나 맘해 보아요.'}, {'id': 99, 'title': '하늘 끝', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '불연듯\n집을 나서 산(山)을 치달아\n바다를 내다보는 나의 신세(身勢)여!\n배는 떠나 하늘로 끝을 가누나!'}, {'id': 100, 'title': '하다못해 죽어 달려가 올라', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '아주 나는 바랄 것 더 없노라\n빛이랴 허공이랴,\n소리만 남은 내 노래를\n바람에나 띄워서 보낼밖에.\n하다못해 죽어 달려가 올라\n좀 더 높은 데서나 보았으면!\n\n한세상 다 살아도\n살은 뒤 없을 것을,\n내가 다 아노라 지금까지\n살아서 이만큼 자랐으니.\n예전에 지나 본 모든 일을\n살았다고 이를 수 있을진댄!\n\n물가의 닳아져 널린 굴꺼풀에\n붉은 가시덤불 뻗어늙고\n어득어득 저문 날을\n비바람에 울지는 돌무더기\n하다못해 죽어 달려가 올라\n밤의 고요한 때라도 지켰으면!'}, {'id': 101, 'title': '합장', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '들이라. 단 두 몸이라. 밤빛은 배여와라.\n아, 이것 봐, 우거진 나무 아래로 달 들어라.\n우리는 말하며 걸었어라, 바람은 부는 대로.\n\n등(燈)불 빛에 거리는 헤적여라, 희미한 하느편에\n고히 밝은 그림자 아득이고\n퍽도 가까인, 풀밭에서 이슬이 번쩍여라.\n\n밤은 막 깊어, 사방(四方)은 고요한데,\n이마즉, 말도 안하고, 더 안가고,\n길가에 우두커니. 눈 감고 마주서서.\n먼 먼 산(山). 산(山)절의 절 종(鍾)소리. 달빛은 지새여라.'}, {'id': 102, 'title': '해가 산마루에 저물어도', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '해가 산(山)마루에 저물어도\n내게 두고는 당신때문에 저뭅니다.\n\n해가 산(山)마루에 올라와도\n내게 두고는 당신때문에 밝은 아침이라고 할 것입니다.\n\n땅이 꺼져도 하늘이 무너져도\n내게 두고는 끝까지 모두다 당신때문에 있습니다.\n  \n다시는, 나의 이러한 맘뿐은, 때가 되면,\n그림자같이 당신한테로 가우리다.\n\n오오, 나의 애인(愛人)이었던 당신이여.'}, {'id': 103, 'title': '후살이', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '홀로된 그 여자(女子)\n근일(近日)에 와서는 후살이 간다 하여라.\n그렇지 않으랴, 그 사람 떠나서\n이제 십년(十年), 저 혼자 더 살은 오늘날에 와서야……\n모두다 그럴듯한 사람사는 일레요.'}, {'id': 104, 'title': '희망', 'author': '김소월', 'year': 1925.0, 'tags': ['한글', '운문'], 'contents': '날은 저물고 눈이 나려라\n낯설은 물가으로 내가 왔을 때.\n산(山)속의 올빼미 울고울며\n떨어진 잎들은 눈 아래로 깔려라\n\n아아 숙살(肅殺)스럽은 풍경(風景)이어\n지혜(智慧)의 눈물을 내가 얻을 때!\n이제금 알기는 알았건마는!\n이 세상 모든 것을\n한갓 아름답은 눈얼님의\n그림자뿐인 줄을.\n\n이우러 향기(香氣)깊은 가을밤에\n우무주러진 나무그림자\n바람과 비가 우는 낙엽(落葉)위에.'}, {'id': 105, 'title': '*님의 부르심을 받들고서', 'author': '김상용', 'year': 1943.0, 'tags': ['한글', '친일'], 'contents': '물결 깨어지는 절벽 이마 위\n가슴 헤치고 서서, 해천(海天) 향해 휘파람 부는 듯\n오랜 구원 이룬 이날의 기쁨이여!\n말 위에 칼을 들고 방가(邦家)의 간성(干城)됨이\n장부의 자랑이어늘, 이제 부름 받았으니,\n젊은이들아 너와 나의 더 큰 광명이 무어랴.\n\n나아가는 너희들 대오에 지축이 울리고,\n복락의 피안으로 깃발은 날린다.\n새 우짖고, 초목조차 환희를 속삭일 제,\n결의와 힘에 넘치는 너의 얼굴에\n아침 태양은 더 정다이 미소하도다.\n\n감벽(紺碧)의 대막(大漠) 위에 점철한 섬과 섬\n거기 10억 동포는 묶여 살았다.\n그 사실 끊이고, 해방의 노래 높은 날,\n낙토(樂土) 건설의 첫 개척을 우리 맡았도다.\n명실 함께 1억선진의 일원이 됨이여,\n어서 저 무잡(蕪雜) 베이고, 여기 꽃을 꽃을 심자.\n\n촉신(燭身)이 타, 일실(一室)에 빛은 넘치고,\n소아(小我) 멸해, 대아(大我)의 거듭남이 있다.\n총에 죽고 의에 살은 열사의 희원,\n피로 네 이름 저 창공에 새겨\n그 꽃다움 천천만대에 전하여라.\n라이선스[편집]'}, {'id': 106, 'title': '적국항복 받고지고', 'author': '김동환', 'year': 1944.0, 'tags': ['한글', '친일', '산문'], 'contents': "1\n\n싸우고 또 싸우며 제3년 들어서다\n이날 아침 간절한 소원 두 가지 있아로라,\n첫째는 올해에 '적국 항복' 꼭 받고 싶고\n둘째는 우리 동포 '지도민족(指導民族)' 되어지이다.\n\n2\n\n'적국 항복' 받기 위해 우리 피 더욱 흘려\n흘린 피 되어요 승(升)에서 두(斗)요 석(石)으로 올리고저,\n바치온 돈도 천 원이요 만 원에서\n백만이요, 억천만 원으로 올리옵고저.\n\n3\n\n'지도민족' 되기 위해 우리 모두 무장하여\n폐하의 주신 검(劍)으로 '조국일본 강토' 지키옵고저\n또 우리 아이 모두 '의무교육' 받아 지혜롭고\n백성들은 '연성(鍊成)' 받아 병농일여(兵農一如)에 달하옵고저.\n\n4\n\n아세아는 부(富)하고 크라, 올해부터\n우리 모두 이땅의 귀인(貴人)이 되고 지도가 되어지이다.\n어서 이 흉적을 물리치고서\n어서 우리 자체(自體)를 닦고 씻어서."}, {'id': 107, 'title': '*님의 부르심을 받들고서', 'author': '김동환', 'year': 1943.0, 'tags': ['한글', '친일'], 'contents': '1\n\n연사는 대농(大農)이요\n싸움은 대첩(大捷)엔데\n이때에 부르시니\n더욱 황송하옵네다.\n\n자라긴 좁은 초가집 구들장 위이나\n인제, 표범같이 뛰어 뵙지요.\n배우긴 소학독본이오나\n인제, 산(山)달같이 일편단심 걸어 뵙지요.\n\n2\n\n5월 담장에\n월계꽃 피듯\n인제, 우리 자녀\n송이송이 피오리다.\n\n누가 감히 낮추어보랴\n님이 쓰실 이 소중한 몸을,\n누가 감히 범하려들랴\n님이 부르실 이 거룩한 자녀들.\n\n앞으로! 어서 앞으로!\n우리 2천 7백만, 님의 앞으로!'}, {'id': 108, 'title': '미영장송곡', 'author': '김동환', 'year': 1942.0, 'tags': ['한자', '친일'], 'contents': '1\n\n물너가라 ᄶᅩ처내라 暴惡米英을\n千里沃野 比律賓도 東洋것이요\n釋迦나신 印度땅도 東洋것이라\n주인두고 너이들은 웨들어왓노\n\n2\n\n물너가라 ᄶᅩ처내라 暴惡米英을\n百餘年을 阿片우에 榮華누리는\n거만스런 홍콩總督 모라내드시\n馬來布哇 印度總督 모다내치자\n\n3\n\n물너가라 ᄶᅩ처내라 暴惡米英을\n亞細亞의 ᄯᅡᆼ우에 익어오르는\n벽한폭 石油한알 다치게말고\n파나마 세즈運河 저ᄶᅩᆨ에몰자'}, {'id': 109, 'title': '소년공의 노래', 'author': '권환', 'year': 1926.0, 'tags': ['한글', '산문'], 'contents': '우리는 나 어린 소년공이다\n\n뼈와 심줄이 아즉도\n봄바람에 자라난 풀대처럼\n연하고 부드러운 나 어린 소년\n부자집 자식같으면\n따뜻한 해빛이 덮여있는 풀밭우에서\n단과자 씹어가며 뛰고놀 나 어린 소년\n\n부자집 자식같으면\n공기좋은 솔숲속 높은 집안에서\n글 배우고 노래부를 나 어린 소년이다\n\n그러나 우리는 지금\n해빛없고 검은 먼지찬 제철공장안\n무겁고 큰 기계앞에서\n\n짜운 땀을 흘리는 소년공이다\n일은 아침부터 느진저녁까지\n\n기계를 돌리고 마치를 뚜다려도\n…은 주인령감의…\n모… 어른의 압…로\n부드러운 …에 푸른 ×피만 남기는것밖에\n아무것도 얻어간것 없는 소년공이다\n\n그렇지만 우리는 잘 안다\n우리와 같이 일하든 많은 아저씨들이\n\n…\n×를 하고 죽도록 싸우다가\n경찰서에 묶여서 잡혀간것을\n우리는 잘 보았다 우리는 잘 안다\n\n동무들아 나 어린 소년공동무들아\n××아프다고 울기만 하지 말고\n×하다고 ××만 하지 말고\n우리도 얼른 힘차게 억세게 자라나서\n\n용감한 그 아저씨들과 같이\n수백만 우리처럼 가난한 사람들\n마른 ×를 ×한터들 ×니기만 하는 동무들\n이리 가나 저리 가나 ×음× …들을 위해서\n싸우자 응 싸우자!'}, {'id': 110, 'title': '가을', 'author': '강경애', 'year': 1925.0, 'tags': ['한글', '산문'], 'contents': '매해 가을마다 울었더니만\n뒷창문 옆에서 울었더니만\n떨어지는 낙엽 좇아 울었더니만\n지금은 그 가을이 또 왔어요\n\n바람에 떨어진 벽에 의하여\n겨울 의복을 꼬매이려고\n힘없는 광선을 바라보면서\n바눌은 번개같이 번쩍이었다\n\n뒷문으로 가만히\n누런빛 사이로 나무꾼 아해\n곰방대를 찬 나무꾼 아해\n가을에 벗님을 찾으펴 해\n\n매해 가을마다 울었더니만\n뒷창문 옆에서 울었더니만\n떨어지는 낙엽 좇아 울었더니만\n지금은 그 가을이 또 왔어요'}, {'id': 111, 'title': '참된 어머니가 되어 주소서', 'author': '강경애', 'year': 1932.0, 'tags': ['한글', '반복', '산문'], 'contents': '어머니\n인편에 들으니\n어머님께서는 마침내\n쫓겨나셨다고요.\n\n어머니\n작년 이때\n우리집 울 뒤 대추나무 가지에는\n대추가 조롱조롱 빨갰을 때\n눈등이 붓도록 우시면서도\n나를 민며느리로 보내었지요.\n\n그때에 어머님께서는\n어머님의 머리 쪄서 판 돈으로\n얼빗과 참빗을 사서\n이 딸의 곳침 속에\n깊이깊이 넣어주시며\n가서 잘살어라! 부대 배 곯지 말어라!\n이것이 마즈막 부탁이었지요.\n\n어머니\n이 집에 온 후로 이 딸은\n꿈이면 어머님과 대추나무를 보았지요\n그리고 일하다가도 멍 하니 행길가를 바라보았답니다.\n지나가는 낯선 손이\n행여나 어머님이나 아닌가 하여서......\n\n어머니\n지금에 알고 보니\n빚값에 이 딸을 파셨다고요\n삼백 냥에 이 딸을 파셨다고요\n그러고도 그 돈 판푼 어머니 손에\n못 쥐어 보셨다고요!\n\n오! 어머니\n저 푸른 하늘을 우러러 물어보세요\n그리고 이 땅을 구르며 물어보세요\n이런 억울한 일을 언제까지나 받아야 옳겠느냐고요?\n\n어머니\n이 딸은 X X 회의 한 사람이 되었답니다.\n그래서 이젠\n어머님도 대추나무도 그립지 않어요?\n이 눈은 X X 회 때문에 빛나고요\n이 팔 이 다리를 굵어지고 있답니다.\n\n어머니\n며칠 후에 내 동무가\n그곳 갈 테니\n부대 잊지 말고 회에 들어주세요\n그래서 나의 참된 어머니가 되어주세요!'}, {'id': 112, 'title': '숲 속의 농부', 'author': '강경애', 'year': 1933.0, 'tags': ['한글', '운문'], 'contents': '김 매던 농부\n허메자루 목침 삼아\n개미들이 토굴 파는\n숲에 누웠소\n\n끊었다 이어지는\n샘물소리에\n포루루 졸음이 오나\n어젯밤 동무가 주던\n종이조각 잊지 않었소\n\n짙은 풀내에 입맛 당겨\n스를 눈 뜨니\n밭이랑에 조싹이 파랑게 보이우'}, {'id': 113, 'title': '오늘의 문득', 'author': '강경애', 'year': 1934.0, 'tags': ['한글', '산문'], 'contents': '가을이 오면은\n내 고향 그리워\n이 마음 단풍잋 같이\n빨개집니다.\n\n오늘 문득 일어나는 생각에 이런 노래를 적어보았지요.'}]
export default testData;