<template>
  <div class="layout1">
      <div :style="{backgroundImage: 'url(' + require('@/assets/Background.png') + ')'}">
        <v-row no-gutters>
          <v-col md="11">
            <v-row no-gutters>
              <v-col>
                <Falling :cur_year='value'/>
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <VueSlider v-model='value' min='1910' max='1945' :lazy='true' :marks="marks">
                  <template v-slot:step="{ label, active }">
                    <div :class="['custom-step', { active }]"></div>
                  </template>
                </VueSlider>
              </v-col>
            </v-row>
            <v-row no-gutters>
              <v-col>
                <v-img :src="require('@/assets/Background_object.png')" align="center">
                  <h1> {{ calEra }} </h1>
                </v-img>
              </v-col>
            </v-row>
            
          </v-col>
          <v-col sm="1">
            <v-img :src="require('@/assets/Side.png')" height="102.2vh"/>
          </v-col>
        </v-row>
      </div>
  </div>
</template>

<script>
import Falling from '@/components/FallingItem';
import VueSlider from 'vue-slider-component';
import 'vue-slider-component/theme/antd.css';
export default {
  name: 'fall_view',
  components:{
    Falling,
    VueSlider
  },
  computed: {
        calEra() {
            if (this.value>=1910 && this.value<=1919){
                return this.cur_era_name='헌병 경찰 통치기';
            }
            else if (this.value>=1920 && this.value<=1931){
                return this.cur_era_name='문화 통치기';
            }
            if (this.value>=1932){
                return this.cur_era_name= '민족 말살 통치기';
            }
        }
    },
  data() {
    return {
      value: 1925,
      cur_era_name: '헌병 경찰 통치기',
      marks: val => val==1919||val==1931
    }
  }


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.custom-step {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #ccc;
  background-color: #fff;
}
.custom-step.active {
  box-shadow: 0 0 0 3px #3498db;
  background-color: #3498db;
}


h1 {
  color: #ffffff;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>